import { Component, OnInit, ViewChild, ElementRef, HostListener, Renderer2 } from '@angular/core';
import { ImageElem, AnnotationMeta, AnnotatedImageData } from '../common/edit-data';

import * as d3 from 'd3';
import { EditService } from '../edit.service';
import { FormGroup, FormBuilder } from '@angular/forms';
import { SB, invertTransform, transform } from '../d3-utils';
import { NetworkService } from '../network.service';
import { Config } from '../common/config';
import { Subject } from 'rxjs';
import { Router } from '@angular/router';
import { ConfigService } from '../config.service';
import { VerificationData } from '../common/verification-data';
import { DemoService } from './demo/demo.service';
import { TimerService } from './timer.service';
import { QuizService } from './demo/quiz.service';

export type EditMode = 'annotation' | 'verification' | 'revision';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    @ViewChild('img', { static: true }) imgRef: ElementRef;
    @ViewChild('imgZone', { static: true }) imgZoneRef: ElementRef;
    @ViewChild('svg', { static: true }) svgRef: ElementRef;

    public config: Config;
    public mode: EditMode = 'verification';
    public selectedImage: ImageElem;
    public formGroup: FormGroup;
    public verificationDatas: VerificationData[] = [];
    public svgS: SB;
    public eventSubject: Subject<string> = new Subject<string>();
    public spacePressed = false;
    public dragging = false;
    public noImagesLeft = false;
    public onImageLoad = false;

    constructor(private edit: EditService,
        private fb: FormBuilder,
        private network: NetworkService,
        private configService: ConfigService,
        private router: Router,
        private renderer: Renderer2,
        private demo: DemoService,
        private quiz: QuizService,
        private timer: TimerService
        ) { }

    ngOnInit() {
        if (!this.configService.userName) {
            this.router.navigate(['/login']);
            return;
        }
        this.svgS = d3.select(this.svgRef.nativeElement);
        this.configService.configObservable
            .subscribe(async (config: Config) => {
                this.config = config;
                this.formGroup = this.initFormGroup(this.config);
                this.eventSubject.next('progress');
                if (config.service !== 'normal') {
                    this.mode = 'annotation';
                }
                switch (this.mode) {
                    case 'annotation':
                        await this.loadNextData();
                        break;
                    case 'verification':
                        const elem = await this.network.getVerificationRandomElem();
                        const result = await this.parseVerification(elem);
                        if (!result) {
                            await this.loadNextData();
                        }
                        break;
                    default:
                        break;
            }
        });

        const imgElem = this.imgRef.nativeElement as HTMLImageElement;
        this.edit.removeImage();
        imgElem.addEventListener('load', () => {
            const boundingBox = (this.imgRef.nativeElement as Element).getBoundingClientRect();
            this.initSVG(boundingBox);
            this.noImagesLeft = false;
            this.onImageLoad = true;
            this.timer.start();

            const path = this.selectedImage.path;
            this.network.getPrediction(path)
                .then(predictData => {
                    this.config.parsePrediction(predictData);
                    if (this.mode === 'annotation') {
                        this.eventSubject.next('predict');
                    }
                });

            if (this.selectedImage.hasPrevAnnotation) {
                const { meta } = this.selectedImage;
                this.config.parsePrevAnnotation(this.formGroup,
                                                meta.annotation,
                                                this.fb);
                this.eventSubject.next('review');
            }
        });

        this.edit.imageSubject.asObservable().subscribe(image => {
            this.formGroup = this.initFormGroup(this.config);
            this.selectedImage = image;
            const svg = this.svgRef.nativeElement as SVGElement;
            this.renderer.setStyle(imgElem, 'transform', transform(1, 1, 0, 0));
            this.renderer.setStyle(svg, 'transform', transform(1, 1, 0, 0));
            if (!image) {
                this.noImagesLeft = !!imgElem.src;
                imgElem.src = '';
                return;
            }
            imgElem.src = image.src;
        });

        this.edit.autoCompleteSubject.asObservable()
            .subscribe(_ => {
                if (this.mode === 'annotation') {
                    this.eventSubject.next('predict');
                }
            });
    }

    async submit(event: Event) {
        this.onImageLoad = false;
        let elem: [AnnotatedImageData, Blob] = null;
        switch (this.config.service) {
            case 'quiz':
                this.gradeQuiz();
                return;
            case 'demo':
                if (this.mode === 'annotation') {
                    elem = await this.demo.getVerificationElem();
                }
                break;
            default:
                await this.timer.sendResult(this.mode);
                await this.network.submit(this.selectedImage,
                                        this.formGroup,
                                        this.config);
                elem = await this.network.getVerificationRandomElem();
                break;
        }
        if (!this.config.verification) {
            await this.loadNextData();
        } else {
            const result = await this.parseVerification(elem);
            if (!result) {
                await this.loadNextData();
            }
        }
    }

    async onRemoveImage() {
        this.timer.reset();
        if (!this.config.verification) {
            await this.loadNextData();
        } else {
            const elem = await this.network.getVerificationRandomElem();
            const result = await this.parseVerification(elem);
            if (!result) {
                await this.loadNextData();
            }
        }
    }


    onReviewSubmitted() {
        this.onImageLoad = false;
        if (this.config.service === 'normal') {
            this.timer.sendResult(this.mode);
        }
        this.loadNextData();
    }

    private async loadNextData() {

        await this.network.clearPending();
        let elem: [AnnotationMeta, Blob] = null;
        switch (this.config.service) {
            case 'quiz':
                elem = await this.quiz.getAnnotationElem();
                break;
            case 'demo':
                elem = await this.demo.getAnnotationElem();
                break;
            default:
                elem = await this.network.getRandomElem();
                this.eventSubject.next('progress');
                break;
        }
        if (elem) {
            const [data, blob] = elem;
            this.mode = data.annotation ? 'revision' : 'annotation';
            this.edit.parseImage(blob, data.path, data);
        } else {
            this.edit.removeImage();
            this.mode = 'annotation';
        }
    }

    private gradeQuiz() {
        const path = this.edit.image.path;
        const [pass, annotation] = this.quiz
            .grade(path, this.formGroup, this.config);
        if (pass) {
            console.log('pass!');
        } else {
            this.mode = 'revision';
            const meta = new AnnotationMeta({
                path,
                feedbacks: [],
                annotation
            });
            this.edit.parseImage(this.edit.image.blob, path, meta);
        }
    }

    private async parseVerification(elem: [AnnotatedImageData, Blob]): Promise<boolean> {
        if (elem) {
            const [data, blob] = elem;
            this.edit.parseImage(blob, data.path);
            this.verificationDatas = this.config.parseVerificationData(data);
            this.mode = 'verification';
            return true;
        } {
            return false;
            // TODO: Implement this
        }
    }

    @HostListener('window:keyup', ['$event'])
    onKey(event: KeyboardEvent) {
        if (event.key === ' ') {
            this.spacePressed = false;
            event.stopImmediatePropagation();
            return false;
        }
    }

    @HostListener('window:keydown', ['$event'])
    onKeyDown(event: KeyboardEvent) {
        if (event.key === ' ') {
            this.spacePressed = true;
        }
    }

    @HostListener('document:pointerdown', ['$event'])
    pointerDown(event: PointerEvent) {
        const svg = this.svgRef.nativeElement as SVGElement;
        const svgRect = svg.getBoundingClientRect();
        this.dragging = event.clientX < svgRect.right
            && event.clientX > svgRect.left
            && event.clientY < svgRect.bottom
            && event.clientY > svgRect.top;
    }

    @HostListener('document:pointerup', ['$event'])
    pointerUp() {
        if (this.dragging) {
            this.dragging = false;
        }
    }

    @HostListener('document:pointermove', ['$event'])
    pointerMove(event: PointerEvent) {
        if (this.dragging && this.spacePressed) {
            const img = this.imgRef.nativeElement as HTMLImageElement;
            const svg = this.svgRef.nativeElement as SVGElement;
            const { sx, sy, tx, ty } = invertTransform(img.style.transform);

            const ntx = tx + event.movementX * (1 / sx);
            const nty = ty + event.movementY * (1 / sy);
            this.renderer.setStyle(img, 'transform', transform(
                sx, sy, ntx, nty));
            this.renderer.setStyle(svg, 'transform', transform(
                sx, sy, ntx, nty));
        }
    }

    @HostListener('mousewheel', ['$event'])
    onImageScroll(event: WheelEvent) {
        const img = this.imgRef.nativeElement as HTMLImageElement;
        const svg = this.svgRef.nativeElement as SVGElement;
        const { sx, sy, tx, ty } = invertTransform(img.style.transform);
        if (this.cursorInRect(event)) {
            const scaleDelta = event.deltaY < 0 ? 0.4 : -0.4;
            const nScaleX = Math.min(5, Math.max(1, sx + scaleDelta));
            const nScaleY = Math.min(5, Math.max(1, sy + scaleDelta));
            const [ox, oy] = this.cursorOffset(event);
            const nTranslateX = event.deltaY < 0
                ? (tx * sx - (nScaleX / sx - 1) * (ox - tx * sx)) / nScaleX
                : tx * (nScaleX - 1) / (sx - 1 + 0.001) * (sx / nScaleX);
            const nTranslateY = event.deltaY < 0
                ? (ty * sy - (nScaleY / sy - 1) * (oy - ty * sy)) / nScaleY
                : ty * (nScaleY - 1) / (sy - 1 + 0.001) * (sy / nScaleY);
            this.renderer.setStyle(img, 'transform', transform(
                nScaleX, nScaleY, nTranslateX, nTranslateY));
            this.renderer.setStyle(svg, 'transform', transform(
                nScaleX, nScaleY, nTranslateX, nTranslateY));
        }
    }

    private cursorInRect(event: WheelEvent): boolean {
        const rect = this.imgZoneRef.nativeElement.getBoundingClientRect();
        const x = event.clientX;
        const y = event.clientY;
        return x >= rect.left
            && x <= rect.right
            && y >= rect.top
            && y <= rect.bottom;
    }

    private cursorOffset(event: WheelEvent): [number, number] {
        const rect = this.imgZoneRef.nativeElement.getBoundingClientRect();
        const rectCenter = {
            x: rect.left + rect.width / 2,
            y: rect.top + rect.height / 2
        };
        return [event.clientX - rectCenter.x, event.clientY - rectCenter.y];
    }

    initFormGroup(config: Config) {
        const formGroup = this.fb.group({});
        config.tasks.forEach(task => {
            task.registerControl(formGroup, this.fb);
        });
        return formGroup;
    }

    private initSVG(boundingBox: ClientRect) {
        this.svgS = d3.select(this.svgRef.nativeElement)
            .attr('width', boundingBox.width + 20)
            .attr('height', boundingBox.height + 20)
            .attr('viewbox', `0 0 ${boundingBox.width + 20} ${boundingBox.height + 20}`)
            .attr('pointer-events', 'visiblePoint');

        this.svgS.select('.border').remove();

        this.svgS.append('rect')
            .attr('class', 'border')
            .attr('width', boundingBox.width + 20)
            .attr('height', boundingBox.height + 20)
            .attr('viewbox', `0 0 ${boundingBox.width + 20} ${boundingBox.height + 20}`)
            .attr('fill-opacity', 0)
            .attr('stroke', '#777')
            .attr('stroke-width', 2)
            .attr('pointer-events', 'none');
    }
}
