import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Feedback } from 'src/app/common/verification-data';

@Component({
    selector: 'app-feedback-mark',
    templateUrl: './feedback-mark.component.html',
    styleUrls: ['./feedback-mark.component.scss']
})
export class FeedbackMarkComponent implements OnInit {

    @Input() feedback: Feedback;
    @Input() focused: boolean;
    @Output() focus = new EventEmitter<Feedback>();

    constructor() { }

    ngOnInit() {

    }

    onClick() {
        this.focus.emit(this.feedback);
    }
}
