import { Component, OnInit, Output, EventEmitter, Input, OnChanges, SimpleChanges } from '@angular/core';
import { Feedback } from 'src/app/common/verification-data';
import { FormGroup } from '@angular/forms';

@Component({
    selector: 'app-review-panel',
    templateUrl: './review-panel.component.html',
    styleUrls: ['./review-panel.component.scss']
})
export class ReviewPanelComponent implements OnInit, OnChanges {

    @Input() feedbacks: Feedback[];
    @Input() feedbackForms: FormGroup[];
    @Input() focusedKey: string;
    @Output() focus = new EventEmitter<string>();

    constructor() { }

    ngOnInit() {

    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.feedbackForms && changes.feedbackForms.currentValue) {
            this.feedbacks = this.feedbackForms.map(d => Feedback.from(d));
        }
    }

    onFeedbackFocused(feedback: Feedback) {
        this.focus.emit(feedback.key);
    }

    equals(form: FormGroup) {
        return Feedback.from(form).key === this.focusedKey;
    }
}
