import { Injectable } from '@angular/core';
import { BehaviorSubject, ReplaySubject } from 'rxjs';
import { Config } from './common/config';

@Injectable({
    providedIn: 'root'
})
export class ConfigService {

    private configSubject = new ReplaySubject<Config>();
    private userNameSubject = new BehaviorSubject<string>(null);
    private checkInstanceSubject = new BehaviorSubject<void>(null);
    constructor() {

    }

    get configObservable() {
        return this.configSubject.asObservable();
    }

    get userNameObservable() {
        return this.userNameSubject.asObservable();
    }

    get checkInstanceObservable() {
        return this.checkInstanceSubject.asObservable();
    }

    get userName() {
        return this.userNameSubject.getValue();
    }

    public setConfig(config: Config) {
        this.configSubject.next(config);
    }

    public setUserName(user: string) {
        this.userNameSubject.next(user);
    }

    public checkInstance() {
        this.checkInstanceSubject.next(null);
    }
}
