import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CustomAnnotation, KV } from 'src/app/common/verification-data';

@Component({
    selector: 'app-custom-spec-card',
    templateUrl: './custom-spec-card.component.html',
    styleUrls: [
        './custom-spec-card.component.scss',
        '../v-overall.component.scss'
    ]
})
export class CustomSpecCardComponent implements OnInit {

    @Input() data: CustomAnnotation;
    @Input() index: number;
    @Input() focusedKey: string;
    @Output() focus = new EventEmitter<string>();

    constructor() { }

    ngOnInit() { }

    emit(el: KV<any>) {
        this.focus.emit(el.key);
    }

    get markLabels(): string {
        if (this.data && this.data.markLabels.value.length > 0) {
            return this.data.markLabels.value.join(', ');
        } else {
            return '';
        }
    }
}
