import { Task, TASK_CLASSIFICATION } from './task';
import { FormGroup, FormBuilder } from '@angular/forms';
import { VClassificationData } from '../verification-data';

export class Classification extends Task {

    public labels: string[];

    constructor(raw: any) {
        super(raw);
        this.taskType = TASK_CLASSIFICATION;
        this.labels = raw['labels'];
    }

    registerControl(formGroup: FormGroup, fb: FormBuilder) {
        formGroup.addControl(this.key, fb.control(this.labels[0]));
    }

    parsePrediction(raw: any) {
        this.prediction = raw;
    }

    parseForVerification(raw: any): VClassificationData {
        return new VClassificationData(this.key, raw['label'], this.labels);
    }

    parsePrevAnnotation(formGroup: FormGroup, raw: any, fb: FormBuilder) {
        formGroup.get(this.key).setValue(raw['label']);
    }

    updateBox(formGroup: FormGroup, raw: any) {
        formGroup.get(this.key).setValue(raw);
    }

    getFormValue(formGroup: FormGroup): {label: string} {
        return {
            label: formGroup.get(this.key).value
        };
    }
}
