import { Component, OnInit, Input, EventEmitter, Output, OnChanges } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatCheckboxChange } from '@angular/material';
import { Feedback } from 'src/app/common/verification-data';

@Component({
    selector: 'app-feedback-card',
    templateUrl: './feedback-card.component.html',
    styleUrls: ['./feedback-card.component.scss']
})
export class FeedbackCardComponent implements OnInit {

    @Input() form: FormGroup;
    @Input() focusedKey: string;
    @Input() readonly comments: string[];

    @Output() remove = new EventEmitter<FormGroup>();
    @Output() focus = new EventEmitter<string>();

    public directInput = false;
    public feedback: Feedback;

    constructor() { }

    ngOnInit() {
        this.feedback = Feedback.from(this.form);
    }

    checkWriteMode(change: MatCheckboxChange) {
        this.directInput = change.checked;
        if (this.directInput) {
            this.form.get('comment').setValue('');
        } else {
            this.form.get('comment').setValue(this.comments[0]);
        }
    }

    onFocus() {
        this.focus.emit(this.focusedKey === this.feedback.key
            ? null
            : this.feedback.key);
    }
}
