import { Component, OnInit, ViewChild, Renderer2, ElementRef, ChangeDetectorRef, AfterViewInit, OnDestroy } from '@angular/core';
import { Feedback } from 'src/app/common/verification-data';
import { FeedbackService } from '../feedback.service';
import { Subscription } from 'rxjs';

@Component({
    selector: 'app-feedback-modal',
    templateUrl: './feedback-modal.component.html',
    styleUrls: ['./feedback-modal.component.scss']
})
export class FeedbackModalComponent implements OnInit, OnDestroy, AfterViewInit {

    @ViewChild('container', { static: false }) ref: ElementRef;

    public feedback: Feedback;
    public displayModal = false;
    private sub: Subscription;

    constructor(private fs: FeedbackService,
                private renderer: Renderer2,
                private changeDetector: ChangeDetectorRef) { }

    ngOnInit() {
    }

    ngAfterViewInit() {
        this.sub = this.fs.showObservable.subscribe(data => {
            this.displayModal = true;
            const { left, top, feedback } = data;
            this.feedback = feedback;
            this.changeDetector.detectChanges();
            const el = this.ref.nativeElement as HTMLDivElement;
            const rect = el.getBoundingClientRect();
            this.renderer.setStyle(el, 'left', `${left - rect.width}px`);
            this.renderer.setStyle(el, 'top', `${top - rect.height / 2}px`);
        });
    }

    close() {
        this.displayModal = false;
    }

    resolve() {
        this.fs.resolveSubject.next(this.feedback);
        this.close();
    }

    ngOnDestroy() {
        if (this.sub) {
            this.sub.unsubscribe();
        }
    }

}
