import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ImageElem, PredictData, ModelInfo, Progress,
    InstanceStatus, MResponse, AnnotatedImageData,
    AnnotationMeta } from './common/edit-data';
import { FormGroup } from '@angular/forms';
import { Config } from './common/config';
import { ConfigService } from './config.service';
import { Review } from './common/verification-data';
import { EditMode } from './edit/edit.component';
import { environment } from 'src/environments/environment';

@Injectable({
    providedIn: 'root'
})
export class NetworkService {

    // private BASE_URL = 'http://localhost:1219/api/';
    private BASE_URL = 'http://147.46.240.21:1219/api/';
    constructor(private http: HttpClient, private configService: ConfigService) { }

    login(key: string, userName: string, password: string): Promise<MResponse<any>> {
        const body = {
            key,
            userName,
            password
        };
        return this.http.post(`${this.BASE_URL}login`, body)
            .toPromise()
            .then(raw => this.parseResponse<any>(raw));
    }

    checkInstance(service: string): Promise<InstanceStatus> {
        return this.httpGetWithUser('check')
            .then((raw: any) => raw as InstanceStatus);
    }

    getModelInfo(): Promise<ModelInfo[]> {
        return this.httpGetWithUser('model_info')
            .then((raw: any[]) => raw.map(d => new ModelInfo(d)));
    }

    getProgress(): Promise<Progress> {
        return this.httpGetWithUser('progress')
            .then((raw: any) => {
                return raw as Progress;
            });
    }

    ignoreImage(imageElem: ImageElem): Promise<any> {
        const body = { path: imageElem.path };
        return this.http.post(`${this.BASE_URL}ignore`, body, this.userOptions)
            .toPromise();
    }

    getPaths(): Promise<string[]> {
        return this.httpGetWithUser('paths').then(d => d as string[]);
    }

    getList(labeled = 0): Promise<AnnotationMeta[]> {
        return this.http.get(`${this.BASE_URL}list?labeled=${labeled}`, this.userOptions)
            .toPromise()
            .then(raw => (raw as any[]).map(d => new AnnotationMeta(d)));
    }

    clearPending(): Promise<any> {
        return this.httpGetWithUser('clear_pending');
    }

    getPrediction(path: string): Promise<PredictData> {
        return this.http.get(`${this.BASE_URL}prediction?path=${path}`, this.userOptions)
            .toPromise()
            .then(raw => new PredictData(raw));
    }

    getImage(path: string): Promise<Blob> {
        return this.http.get(`${this.BASE_URL}image?path=${path}`, {responseType: 'blob', ...this.userOptions})
            .toPromise();
    }

    getRandomElem(): Promise<[AnnotationMeta, Blob]> {
        return this.httpGetWithUser('random_image')
            .then((raw: any) => this.parseResponse<AnnotationMeta>(raw))
            .then((response: MResponse<AnnotationMeta>) => {
                const data = response.data;
                if (data) {
                    return Promise.all([data, this.getImage(data.path)]);
                } else {
                    return null;
                }
            });
    }

    getVerificationRandomElem(): Promise<[AnnotatedImageData, Blob]> {
        return this.httpGetWithUser('verification_image')
            .then((response: MResponse<AnnotatedImageData>) => {
                if (response.data) {
                    return new AnnotatedImageData(response.data);
                } else {
                    return null;
                }
            })
            .then((meta?: AnnotatedImageData) => {
                if (meta) {
                    return Promise.all([meta, this.getImage(meta.path)]);
                } else {
                    return null;
                }
            });
    }

    submit(imageElem: ImageElem, formGroup: FormGroup, config: Config): Promise<any> {
        const body = { raw: {}, path: imageElem.path, semi: imageElem.semi };

        for (const task of config.tasks) {
            const key = task.key;
            body.raw[key] = task.getFormValue(formGroup);
        }
        return this.http.post(this.BASE_URL + 'submit', body, this.userOptions)
            .toPromise().then(() => null);
    }

    registerReview(review: Review): Promise<MResponse<any>> {
        return this.http.post(this.BASE_URL + 'register_review',
                              review,
                              this.userOptions)
                    .toPromise().then(d => this.parseResponse(d));
    }

    getReview(path: string): Promise<Review> {
        return this.http.get(`${this.BASE_URL}review?path=${path}`)
            .toPromise()
            .then(d => new Review(d));
    }

    sendElapsedTime(mode: EditMode, time: number): Promise<any> {
        return this.http.post(`${this.BASE_URL}time`, { mode, time })
            .toPromise();
    }

    async loadLocalImage(path: string): Promise<Blob> {
        return this.http.get(path, { responseType: 'blob' })
            .toPromise();
    }

    private httpGetWithUser(path: string) {
        return this.http.get(`${this.BASE_URL}${path}`, this.userOptions).toPromise();
    }

    private get userOptions(): { headers: HttpHeaders } {
        let headers = new HttpHeaders();
        const userName = this.configService.userName;
        headers = headers.append('service', environment.mode);
        if (userName) {
            headers = headers.append('Username', userName);
        }
        return { headers };
    }

    private parseResponse<T>(raw: any, className?: (new (raw: any) => T)): MResponse<T> {
        let data: T;
        if (!raw['data']) {
            return {
                message: raw['message'],
                code: raw['code'],
                data: null
            };
        }

        try {
            if (className) {
                data = new className(raw['data']);
            } else {
                data = raw['data'] as T;
            }
        } catch (err) {
            data = raw['data'] as T;
        }

        return {
            message: raw['message'],
            code: raw['code'],
            data
        };
    }
}
