import { Component, OnInit, Input, ElementRef,
    OnChanges, SimpleChanges, HostListener, ViewChild, Output,
    EventEmitter, ViewChildren, QueryList, ChangeDetectorRef, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, FormArray } from '@angular/forms';
import { SB, SA } from 'src/app/d3-utils';
import { Overall, BoxData, KEY_LEGEND_TITLE, KEY_LEGEND_LABEL, KEY_TITLE, KEY_MARK_LABEL } from 'src/app/common/task';
import { Observable, Subscription } from 'rxjs';
import { AnnotationBox, removeEventArea, createEventArea,
    addRectDragEvent, addMergeEvent, regressBoxesByColor, drawBoxBySelectedPixel,
    boxComparator, isNumberTexts, NUM_REG, removeTempBox, removeMergeLine } from '../annotation-util';
import { EditService } from 'src/app/edit.service';

import * as d3 from 'd3';
import { MatButtonToggleChange, MatCheckboxChange, MatSliderChange } from '@angular/material';
import { FeedbackService } from '../../common/feedback.service';
import { Feedback } from 'src/app/common/verification-data';
import { isNumber } from 'util';
import { EditMode } from '../../edit.component';

@Component({
    selector: 'app-overall',
    templateUrl: './overall.component.html',
    styleUrls: [
        './overall.component.scss',
        '../../edit-module.scss'
    ]
})
export class OverallComponent implements OnInit, OnChanges, OnDestroy {

    @Input() formGroup: FormGroup;
    @Input() task: Overall;
    @Input() index: number;
    @Input() svgS: SB;
    @Input() imgRef: ElementRef;
    @Input() currIndex: number;
    @Input() event: Observable<string>;
    @Input() spacePressed: boolean;
    @Input() mode: EditMode;
    @Output() submitEmitter = new EventEmitter<void>();

    @ViewChild('pointMark', { static: false }) pointMark: ElementRef;
    @ViewChild('textScroll', { static: false }) textScrollRef: ElementRef;
    @ViewChild('markScroll', { static: false }) markScrollRef: ElementRef;
    @ViewChild('specScroll', { static: false }) specScrollRef: ElementRef;

    @ViewChildren('markRows') markRowsRef: QueryList<ElementRef>;
    @ViewChildren('textRows') textRowsRef: QueryList<ElementRef>;

    // common
    public auto = false;
    public visited = [true, false, false];
    public focusedFeedbackKey = '';
    public focusedFeedback: Feedback = null;

    // Stage 1
    public focusedBoxIndex: string = null;
    public autoIncBoxIndex = 0;
    public boxes: AnnotationBox[] = [];
    public marks: AnnotationBox[] = [];
    public texts: AnnotationBox[] = [];
    public isDrawing = false;
    public isMerging = false;
    public g: SB;
    public boxMode: 'text' | 'mark' = 'text';
    public pointRadius = 8;
    public subscriptions: Subscription[] = [];

    private _stage = 0;
    constructor(private edit: EditService,
                private fb: FormBuilder,
                private fs: FeedbackService,
                private changeDetector: ChangeDetectorRef) { }

    ngOnInit() {
        const sub1 = this.event.subscribe(key => {
            this.isDrawing = false;
            this.isMerging = false;
            if (key === 'predict' &&
                this.edit.autoCompleteSubject.getValue()[this.task.key]) {
                this.task.registerControl(this.formGroup, this.fb);
                this.task.applyPrediction(this.formGroup);
                this.updateBoxes();
                if (this.currIndex === this.index) {
                    this.createEventArea();
                }
            }

            if (key === 'review') {
                this.updateBoxes();
                this.createEventArea();
            }

            if (key === 'finish') {
                this.reset();
                removeEventArea(this);
            }
        });
        this.subscriptions.push(sub1);

        const sub2 = this.edit.autoCompleteSubject.asObservable()
            .subscribe(map => {
                this.auto = map[this.task.key];
            });
        this.subscriptions.push(sub2);

        const sub3 = this.fs.feedbackObservable.subscribe(feedback => {
            this.focusedFeedback = null;
            this.focusedFeedbackKey = null;
            if (feedback.taskKey === this.task.key) {
                const splited = feedback.annotationKey.split('.');
                switch (splited[0]) {
                    case 'mark': {
                        this.stage = 0;
                        if (splited[1] === 'other') {
                            this.focusedFeedback = feedback;
                            this.focusedFeedbackKey = feedback.annotationKey;
                            this.boxMode = 'mark';
                            this.updateFocus(null);
                        } else {
                            const markIndex = +splited[1];
                            const key = this.marks[markIndex].key;
                            this.focusedFeedback = feedback;
                            this.focusedFeedbackKey = key;
                            this.updateFocus(key);
                        }
                        break;
                    }
                    case 'text': {
                        this.stage = 0;
                        if (splited[1] === 'other') {
                            this.focusedFeedback = feedback;
                            this.focusedFeedbackKey = feedback.annotationKey;
                            this.boxMode = 'text';
                            this.updateFocus(null);
                        } else {
                            const textIndex = +splited[1];
                            const key = this.texts[textIndex].key;
                            this.focusedFeedback = feedback;
                            this.focusedFeedbackKey = key;
                            this.updateFocus(key);
                        }
                        break;
                    }
                    case 'spec': {
                        if (splited[splited.length - 1] === 'encoding') {
                            this.stage = 2;
                        } else {
                            this.stage = 1;
                        }
                        if (splited[1] === 'other') {
                            this.focusedFeedback = feedback;
                            this.focusedFeedbackKey = feedback.annotationKey;
                            this.updateFocus(null);
                        }

                        if (splited[1] === 'title') {
                            this.focusedFeedback = feedback;
                            this.focusedFeedbackKey = 'title';
                        }
                        this.changeDetector.detectChanges();
                        this.updateFocus(null);
                        break;
                    }
                    case 'raw': {
                        this.stage = 2;
                        this.changeDetector.detectChanges();
                        this.updateFocus(null);
                        break;
                    }
                    default:
                        break;
                }
            }
        });
        this.subscriptions.push(sub3);
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.currIndex || changes.formGroup) {
            this.updateBoxes();
            if (this.currIndex === this.index) {
                this.createEventArea();
                this.reset();
            } else {
                removeEventArea(this);
            }
        }
    }

    @HostListener('window:keyup', ['$event'])
    onKey(event: KeyboardEvent) {

        if (document.activeElement.tagName === 'INPUT') {
            return;
        }

        if (event.key === 'Escape' && this.isMerging) {
            removeMergeLine(this);
        }

        if (event.key === 'Escape' && this.isDrawing) {
            removeTempBox(this);
        }

        if ((event.key === 'Delete' || event.key === 'd')
            && this.focusedBoxIndex && this._stage === 0) {
            this.deleteBox(this.focusedBoxIndex);
        }

        if (event.key === 'f' && this.focusedBoxIndex && this._stage === 0) {
            this.sortDrawOrder(this.focusedBoxIndex);
        }

        if (event.key === 'b' && this.focusedBoxIndex && this._stage === 0) {
            this.sortDrawOrder(this.focusedBoxIndex, false);
        }
    }

    reset() {
        this._stage = 0;
        this.visited = [true, false, false];
        this.autoIncBoxIndex = d3.max(this.boxes.map(box => +box.key)) || 0;
        this.focusedBoxIndex = null;
        this.boxMode = 'text';
    }

    deleteBox(boxIndex: string) {
        this.g.select(`.rect-${boxIndex}`).remove();
        this.g.selectAll(`.circle-${boxIndex}`).remove();
        this.task.deleteBox(this.formGroup, boxIndex);
        if (boxIndex === this.focusedBoxIndex) {
            this.focusedBoxIndex = null;
        }
        this.updateBoxes();
    }

    inputKeyPress(event: KeyboardEvent) {
        if (event.key === 'Enter') {
            event.preventDefault();
            event.stopPropagation();
        }
    }

    onBlurEvent(box: AnnotationBox) {
        if (this.focusedBoxIndex !== box.key) {
            this.updateFocus(box.key);
        }
    }

    removeBox(boxIndex: string) {
        this.g.select(`.rect-${boxIndex}`).remove();
        this.g.selectAll(`.circle-${boxIndex}`).remove();
        this.task.deleteBox(this.formGroup, boxIndex);
        this.focusedBoxIndex = null;
    }

    private createEventArea() {
        removeEventArea(this);
        createEventArea(this, true, this.task.inputType);
        this.svgS.on('contextmenu', () => {
            if (this.isMerging) {
                removeMergeLine(this);
                d3.event.preventDefault();
            } else if (this.task.inputType === 'rect'
                && this.boxMode === 'mark') {
                drawBoxBySelectedPixel(this);
            }

            d3.event.preventDefault();
            d3.event.stopPropagation();
            return false;
        });
    }

    onLabelChange() {
        this.updateBoxes();
        this.updateFocus(this.focusedBoxIndex);
    }

    addRectEvent(boxIndex: string, rectS: SB, circleS: SA) {
        addRectDragEvent(this, boxIndex, rectS, circleS);
        if (this.boxes.find(d => d.key === boxIndex).isText) {
            addMergeEvent(this, boxIndex, rectS);
        }
    }

    writeForm(boxIndex: string, x: number, y: number, width: number, height: number) {
        const imgElem = this.imgRef.nativeElement;
        const clamp = (value: number, min: number, max: number) => {
            return Math.min(max, Math.max(min, value));
        };
        const raw: BoxData = {
            key: boxIndex,
            x0: clamp(x - 10, 0, imgElem.width) / imgElem.width,
            y0: clamp(y - 10, 0, imgElem.height) / imgElem.height,
            x1: clamp((x + width) - 10, 0, imgElem.width) / imgElem.width,
            y1: clamp((y + height) - 10, 0, imgElem.height) / imgElem.height,
            text: this.boxMode === 'mark' ? undefined : ''
        };
        const addNewBox = !this.formGroup
            .get(this.task.key)
            .get('boxes')
            .get(raw.key);

        this.task.updateBox(this.formGroup, raw);
        if (addNewBox) {
            this.updateBoxes();
        }
    }

    updateFocus(boxIndex: string) {

        this.g.selectAll('rect').classed('focused', false);
        this.g.selectAll('circle').classed('focused', false);
        this.g.select(`.rect-${boxIndex}`).classed('focused', true);
        this.g.selectAll(`.circle-${boxIndex}`).classed('focused', true);
        this.focusedBoxIndex = boxIndex;
        const box = this.boxes.find(d => d.key === this.focusedBoxIndex);
        if (!box) {
            return;
        }
        if (box.isText) {
            this.boxMode = 'text';
        } else {
            this.boxMode = 'mark';
        }
        this.changeDetector.detectChanges();
        if (this.markScrollRef) {
            const el = this.markScrollRef.nativeElement as HTMLDivElement;
            const elRect = el.getBoundingClientRect();
            const focusedDiv = d3.select(el).select(`.elem-${boxIndex}`).node() as HTMLDivElement;
            if (focusedDiv) {
                const focusedRect = focusedDiv.getBoundingClientRect();
                if (focusedRect.top > elRect.bottom - 50
                    || focusedRect.bottom < elRect.top + 50) {
                    el.scroll({top: focusedDiv.offsetTop - 150});
                }
            }
        }

        if (this.textScrollRef) {
            const el = this.textScrollRef.nativeElement as HTMLDivElement;
            const elRect = el.getBoundingClientRect();
            const rowHeight = 38;
            const headerHeight = 33;
            const findIndex = this.boxes
                .filter(d => d.isText)
                .findIndex(d => d.key === this.focusedBoxIndex);
            const focusedBottom = rowHeight * (findIndex + 1);
            const focusedTop = rowHeight * findIndex;
            const elRange = [
                el.scrollTop - headerHeight - 10,
                elRect.bottom - elRect.top + el.scrollTop - headerHeight + 10
            ];

            if (focusedBottom >= elRange[1] || focusedTop <= elRange[0]) {
                el.scroll({top: focusedTop});
            }
        }
    }

    updateBoxes() {
        const formGroup = this.formGroup
            .get(this.task.key).get('boxes') as FormGroup;
        this.boxes = Object
            .entries(formGroup.controls)
            .map(entry => {
                const [key, value] = entry;
                return new AnnotationBox(key, value as FormGroup);
            })
            .sort(boxComparator);

        this.marks = this.boxes.filter(d => !d.isText);
        this.texts = this.boxes.filter(d => d.isText);
    }

    clearBoxes() {
        const deletedBoxIndices = this.boxes.filter(d => {
            return this.boxMode === 'mark'
                ? d.text === null
                : d.text !== null;
        })
        .map(d => d.key);
        deletedBoxIndices.forEach(boxIndex => {
            this.g.select(`.rect-${boxIndex}`).remove();
            this.g.selectAll(`.circle-${boxIndex}`).remove();
            this.task.deleteBox(this.formGroup, boxIndex);
        });
        this.updateBoxes();
    }

    box(boxIndex: string): FormGroup {
        return this.formGroup.get(this.task.key).get('boxes').get(boxIndex) as FormGroup;
    }

    onBoxModeChange(event: MatButtonToggleChange) {
        this.boxMode = event.value;
    }

    onChangeTitleSpec(event: Event) {
        const value = (event.target as HTMLInputElement).value;
        const titleBox = this.boxes.find(d => d.label.value === KEY_TITLE);
        if (titleBox) {
            titleBox.text.setValue(value);
        }
    }

    predictStage2() {
        (this.formGroup.get(this.task.key) as FormGroup)
            .setControl('spec', this.task.getDefaultSpecForm(this.fb));
        this.detectAxes();
        this.mapBoxToTitle();
        this.mapBoxesToLegend();
        if (!this.hasLegend && !this.hasXAxis && !this.hasYAxis) {
            this.addCustomColumn(true);
        }
    }

    goNextStage() {
        if (this._stage === 0 && !this.visited[1] && this.mode === 'annotation') {
            this.predictStage2();
        }
        this.stage = Math.min(2, this._stage + 1);
    }

    goPrevStage() {
        this.stage = Math.max(0, this._stage - 1);
    }

    refresh(index: number) {
        switch (index) {
            case 1:
                this.predictStage2();
                break;
            default:
                break;
        }
    }

    addAxis(dir = 'x', title = '', type = 'nominal', labels = [],
            pre = 0, valuePerPixel = 0) {
        const formGroup = this.formGroup.get(this.task.key)
            .get('spec').get('axis') as FormArray;

        let unit = pre <= 0 ? 'integer' : 'decimal';
        if (type === 'nominal') {
            unit = '';
        }
        const newGroup = this.fb.group({
            dir: this.fb.control(dir),
            title: this.fb.control(title),
            type: this.fb.control(type),
            labels: this.fb.array(labels),
            precision: this.fb.control(pre),
            unit: this.fb.control(unit),
            labeled: this.fb.control(false),
            markLabels: this.fb.array([]),
            valuePerPixel: this.fb.control(valuePerPixel),
        });
        formGroup.push(newGroup);
    }

    addCustomColumn(labeled = false) {
        const formGroup = this.formGroup.get(this.task.key)
            .get('spec').get('custom') as FormArray;

        const newGroup = this.fb.group({
            title: this.fb.control(`Unknown column${formGroup.length + 1}`),
            type: this.fb.control('nominal'),
            labeled: this.fb.control(labeled),
            markLabels: this.fb.array([]),
            unit: this.fb.control('integer'),
        });
        formGroup.push(newGroup);
    }

    deleteCustomColumn(index: number) {
        this.customGroups.splice(index, 1);
    }

    private detectAxes() {
        const axesGroup = (this.formGroup.get(this.task.key)
            .get('spec').get('axis') as FormArray);

        while (axesGroup.length) {
            axesGroup.removeAt(0);
        }

        const { width, height } = this.imgRef.nativeElement as HTMLImageElement;
        const getPrecision = (v: string) => {
            const splited = v.split('.');
            if (splited.length === 2) {
                return splited[1].length;
            }
            const vs = splited[0];
            for (let i = vs.length - 1; i >= 0; i--) {

                if (vs.charAt(i) !== '0') {
                    return i - vs.length + 1;
                }
            }
            return 0;
        };

        const interpret = (axis: AnnotationBox[], dir: 'x' | 'y', title: string) => {
            if (isNumberTexts(axis)) {
                const axisValues = axis.map(d => {
                    return {
                        value: +d.text.value.replace(NUM_REG, ''),
                        center: d.center
                    };
                }).sort((a, b) => a.value - b.value);
                const pre = d3.max(axisValues.filter(d => d.value !== 0), d => getPrecision(d.value.toString())) + 1;
                const valuesPerPixel: number[] = [];
                for (let i = 0; i < axisValues.length - 1; i++) {
                    const curr = axisValues[i];
                    const next = axisValues[i + 1];
                    const interval = Math.abs(dir === 'x'
                        ? (curr.center[0] - next.center[0]) * width
                        : (curr.center[1] - next.center[1]) * height);
                    valuesPerPixel.push((+next.value - +curr.value) / interval);
                }
                const valuePerPixel = +d3.mean(valuesPerPixel);
                const labels = this.boxes.filter(d => d.label.value === `${dir}-axis-label`).map(d => d.text.value);
                this.addAxis(dir, title, 'quantitative', labels, pre, valuePerPixel);
            } else {
                const labels = this.boxes.filter(d => d.label.value === `${dir}-axis-label`).map(d => d.text.value);
                this.addAxis(dir, title, 'nominal', labels);
            }
        };

        const detectAxis = (dir: 'x' | 'y') => {

            const axis = this.boxes.filter(d => d.label.value === `${dir}-axis-label`);
            let title = `Unknown ${dir.toUpperCase()} Axis`;
            const axisTitleBox = this.boxes.find(d => d.label.value === `${dir}-axis-title`);
            if (axisTitleBox !== undefined) {
                title = axisTitleBox.text.value;
            }

            if (axis.length > 0) {
                interpret(axis, dir, title);
                return true;
            } else {
                return false;
            }
        };

        detectAxis('x');
        detectAxis('y');
        if (this.hasXAxis || this.hasYAxis) {
            this.mapMarkLabelToAxis();
        }
    }

    onCheckBoxRegression(change: MatCheckboxChange) {
        if (change.checked) {
            regressBoxesByColor(this);
        }
    }

    onPointSizeChange(change: MatSliderChange) {
        const pointSymbol = this.pointMark.nativeElement as SVGCircleElement;
        const r = change.value;
        d3.select(pointSymbol).attr('r', r);
        this.boxes.filter(box => box.text === null).forEach(mark => {
            const boxIndex = mark.key;
            const sel = this.g.select(`.rect-${boxIndex}`);
            sel.attr('r', r);
            const x = +sel.attr('cx');
            const y = +sel.attr('cy');
            this.writeForm(boxIndex, x - r, y - r, 2 * r, 2 * r);
        });
        this.updateBoxes();
    }

    private mapBoxToTitle() {
        const titleBox = this.boxes.find(d => d.label.value === KEY_TITLE);
        const titleControl = this.formGroup.get(this.task.key)
            .get('spec.title');
        if (titleBox !== undefined) {
            titleControl.setValue(titleBox.text.value);
        } else if (titleControl.value === '') {
            titleControl.setValue('Unknown Title');
        }
    }

    private mapBoxesToLegend(): boolean {
        const titleBox = this.boxes.find(d => d.label.value === KEY_LEGEND_TITLE);
        const legendBoxes = this.boxes.filter(d => d.label.value === KEY_LEGEND_LABEL);
        if (legendBoxes.length === 0) {
            return false;
        }
        const group = Overall.legendDefaultForm(this.fb);
        group.get('title').setValue(titleBox ? titleBox.text.value : 'Unknown Legend');
        legendBoxes.map(d => d.text.value).forEach(legend => {
            (group.get('labels') as FormArray).push(this.fb.control(legend));
        });
        this.specGroup.setControl('legend', group);
        return true;
    }

    private mapMarkLabelToAxis() {
        const markLabelBoxes = this.boxes.filter(d => {
            return d.isText && d.label.value === KEY_MARK_LABEL;
        });

        const markLabelType = isNumberTexts(markLabelBoxes)
            ? 'quantitative'
            : 'nominal';
        const axesGroup = this.specGroup.get('axis') as FormArray;
        axesGroup.controls.forEach((group: FormGroup) => {
            if ((group.get('type').value as string) === markLabelType) {
                if (markLabelType === 'quantitative') {
                    const values = (group.get('labels') as FormArray)
                        .controls.map(d => {
                            return +d.value.replace(NUM_REG, '');
                        });
                    const extent = d3.extent(values);
                    const numIncludedValues = markLabelBoxes
                        .map(d => +d.text.value.replace(NUM_REG, ''))
                        .map(d => d >= extent[0] && d <= extent[1] ? 1 : 0)
                        .reduce((acc, curr) => acc + curr, 0);
                    const labeled = numIncludedValues > 0.5 * markLabelBoxes.length;
                    group.get('labeled').setValue(labeled);
                    const markLabelsForm = group.get('markLabels') as FormArray;
                    markLabelBoxes.forEach(box => {
                        markLabelsForm.push(this.fb.control(box.text.value));
                    });
                } else {
                    const axisLabels = group.get('labels').value as string[];
                    const allIncluded = markLabelBoxes.every(d => {
                        return axisLabels.includes(d.text.value);
                    });
                    group.get('labeled').setValue(allIncluded);
                    const markLabelsForm = group.get('markLabels') as FormArray;
                    markLabelBoxes.forEach(box => {
                        markLabelsForm.push(this.fb.control(box.text.value));
                    });
                }
            } else {
                group.addControl('labeled', this.fb.control(false));
            }
        });
    }

    private sortDrawOrder(boxIndex: string, forward = true) {
        if (forward) {
            this.g.select(`.rect-${boxIndex}`).raise();
            this.g.selectAll(`.circle-${boxIndex}`).raise();
        } else {
            this.g.select(`.rect-${boxIndex}`).lower();
            this.g.selectAll(`.circle-${boxIndex}`).lower();
        }
    }

    onSubmitReady() {
        if (this.index === Object.keys(this.formGroup.value).length - 1) {
            this.submitEmitter.emit();
        }
    }

    onSpecScroll(top: number) {
        const scrollEl = this.specScrollRef.nativeElement as HTMLElement;
        this.specScrollRef.nativeElement.scroll({
            top: top - 500 + scrollEl.scrollTop
        });
    }

    ngOnDestroy() {
        this.subscriptions.forEach(sub => {
            sub.unsubscribe();
        })
    }

    get specGroup(): FormGroup {
        return this.formGroup.get(this.task.key).get('spec') as FormGroup;
    }

    get axesGroup(): FormGroup[] {
        return (this.formGroup.get(this.task.key)
            .get('spec').get('axis') as FormArray).controls as FormGroup[];
    }

    get customGroups(): FormGroup[] {
        return (this.specGroup.get('custom') as FormArray)
            .controls as FormGroup[];
    }

    get legendGroup(): FormGroup {
        return (this.formGroup.get(this.task.key)
            .get('spec').get('legend') as FormGroup);
    }

    get hasLegend(): boolean {
        return this.specGroup.contains('legend');
    }

    get hasXAxis(): boolean {
        return this.axesGroup.some(form => form.get('dir').value === 'x');
    }

    get hasYAxis(): boolean {
        return this.axesGroup.some(form => form.get('dir').value === 'y');
    }

    get stage() {
        return this._stage;
    }

    set stage(value: number) {
        this._stage = value;
        this.visited[value] = true;
    }
}
