import { Component, OnInit, Input, ViewChild, ElementRef, Output, EventEmitter, AfterViewInit, OnDestroy } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Overall } from 'src/app/common/task';
import { Feedback } from 'src/app/common/verification-data';
import { Subscription } from 'rxjs';
import { FeedbackService } from 'src/app/edit/common/feedback.service';

@Component({
    selector: 'app-legend-spec',
    templateUrl: './legend-spec.component.html',
    styleUrls: [
        './legend-spec.component.scss',
        '../overall.component.scss'
    ]
})
export class LegendSpecComponent implements OnInit, AfterViewInit, OnDestroy {

    @Input() auto: boolean;
    @Input() group: FormGroup;
    @Input() task: Overall;

    @ViewChild('title', { static: false }) titleRef: ElementRef;
    @ViewChild('labels', { static: false }) labelsRef: ElementRef;

    @Output() scroll = new EventEmitter<number>();

    public focusedFeedbackKey: string;
    public focusedFeedback: Feedback;

    private refMap: { [key: string]: ElementRef };
    private sub: Subscription;

    constructor(private fs: FeedbackService) { }

    ngOnInit() {
        this.sub = this.fs.feedbackObservable.subscribe(feedback => {
            this.focusedFeedbackKey = '';
            this.focusedFeedback = null;
            if (feedback.taskKey !== this.task.key) {
                return;
            }
            const splited = feedback.annotationKey.split('.');
            if (splited[0] !== 'spec' ||
                splited[1] !== 'legend') {
                return;
            }

            this.focusedFeedbackKey = splited[2];
            this.focusedFeedback = feedback;
            setTimeout(() => {
                this.scrollEvent();
            }, 0);
        });
    }

    ngAfterViewInit() {
        this.refMap = {
            'title': this.titleRef,
            'labels': this.labelsRef
        };
    }

    scrollEvent() {
        const ref = this.refMap[this.focusedFeedbackKey];
        if (!ref) {
            return;
        }
        const el = ref.nativeElement as HTMLElement;
        const top = el.getBoundingClientRect().top;
        this.scroll.emit(top);
    }

    ngOnDestroy() {
        if (this.sub) {
            this.sub.unsubscribe();
        }
    }

    focusedElemWithKey(key: string): boolean {
        return !!this.focusedFeedback && this.focusedFeedbackKey === key;
    }

}
