import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { VClassificationData, Feedback } from 'src/app/common/verification-data';
import { Classification } from 'src/app/common/task';
import { FormGroup, FormBuilder } from '@angular/forms';

@Component({
    selector: 'app-v-classification',
    templateUrl: './v-classification.component.html',
    styleUrls: [
        './v-classification.component.scss',
        '../verification-module.component.scss'
    ]
})
export class VClassificationComponent implements OnInit {

    @Input() task: Classification;
    @Input() data: VClassificationData;

    @Output() addFeedback: EventEmitter<FormGroup> = new EventEmitter();
    @Output() removeFeedback: EventEmitter<FormGroup> = new EventEmitter();

    public feedbackForm: FormGroup;

    constructor(private fb: FormBuilder) { }

    ngOnInit() {

    }

    remove() {
        this.removeFeedback.emit(this.feedbackForm);
        this.feedbackForm = null;
    }

    clickLabelButton(label: string) {
        if (label === this.data.label) {
            this.update(`Label [${label}] might not be selected`);
        } else {
            this.update(`Label [${label}] might be selected`);
        }
    }

    update(comment: string) {
        if (this.feedbackForm) {
            this.feedbackForm.get('comment').setValue(comment);
        } else {
            this.feedbackForm = this.fb.group({
                taskKey: this.task.key,
                annotationKey: 'label',
                comment: this.fb.control(comment)
            });
            this.addFeedback.emit(this.feedbackForm);
        }
    }
}
