import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

class ClusterResult {
    centers: number[][];
    labels: number[];
    centerPatches: number[][][];

    constructor(res: any) {
        this.centers = res['center_colors'] || [];
        this.centerPatches = res['center_patches'] || [];
        this.labels = res['labels'];
    }
}

@Injectable({
    providedIn: 'root'
})
export class PredictionService {

    private BASE_URL = 'http://147.46.240.21:1219/api/';
    constructor(private http: HttpClient) {

    }

    async clusterMarks(body: any): Promise<ClusterResult> {
        const response = await this.http.post(`${this.BASE_URL}cluster_marks`, body).toPromise();
        return new ClusterResult(response);
    }

    async predictAreas(body: any): Promise<number[]> {
        return this.http.post(`${this.BASE_URL}predict_area`, body)
            .toPromise().then(d => d as number[]);
    }
}
