import { Component, OnInit, ViewChild, ElementRef, HostListener, Renderer2, Input, Output, EventEmitter, OnChanges, SimpleChanges, OnDestroy } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatStepper } from '@angular/material';
import { Config } from 'src/app/common/config';
import { SA } from 'src/app/d3-utils';
import { Observable, Subject, Subscription } from 'rxjs';
import { StepperSelectionEvent } from '@angular/cdk/stepper';
import { Review, Feedback } from 'src/app/common/verification-data';
import { EditService } from 'src/app/edit.service';
import { NetworkService } from 'src/app/network.service';
import { FeedbackService } from '../common/feedback.service';
import { DemoService } from '../demo/demo.service';
import { QuizService } from '../demo/quiz.service';

@Component({
    selector: 'app-annotation-module',
    templateUrl: './annotation-module.component.html',
    styleUrls: ['./annotation-module.component.scss']
})
export class AnnotationModuleComponent implements OnInit, OnChanges, OnDestroy {

    @Input() formGroup: FormGroup;
    @Input() config: Config;
    @Input() imgRef: ElementRef;
    @Input() spacePressed: boolean;
    @Input() svgS: SA;
    @Input() event: Observable<string>;
    @Input() mode: 'annotation' | 'verification' | 'revision';

    @Output() submitEmitter = new EventEmitter();

    @ViewChild('stepper', { static: true }) stepper: MatStepper;

    public currIndex = 0;
    public submitReady = false;
    public review: Review;
    public focusedFeedbackKey = '';

    public sub1: Subscription;
    public sub2: Subscription;

    constructor(private network: NetworkService,
                private edit: EditService,
                private fs: FeedbackService,
                private demo: DemoService,
                private quiz: QuizService) { }

    ngOnInit() {
        this.resetSubmitReady();
        this.sub1 = this.event.subscribe(async key => {
            if (key === 'review') {
                const path = this.edit.image.path;
                switch (this.config.service) {
                    case 'normal':
                        this.review = await this.network.getReview(path);
                        break;
                    case 'demo':
                        this.review = await this.demo.getReview(path);
                        break;
                    case 'quiz':
                        this.review = this.quiz.currReview;
                        break;
                }
            }
        });

        this.sub2 = this.fs.resolveObservable.subscribe((feedback: Feedback) => {

            const findIndex = this.review.feedbacks.findIndex(d => {
                console.log(d.annotationKey, d.taskKey);
                return d.annotationKey === feedback.annotationKey &&
                d.taskKey === feedback.taskKey;
            });
            this.review.feedbacks.splice(findIndex, 1);
        });
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.mode) {
            this.resetSubmitReady();
        }
    }

    displayFeedback(key: string) {
        if (this.focusedFeedbackKey === key) {
            this.focusedFeedbackKey = '';
            this.fs.feedbackSubject.next(Feedback.dummy());
            return;
        }
        this.currIndex = this.config.tasks
            .findIndex(d => d.key === key.split('.')[0]);
        this.stepper.selectedIndex = this.currIndex;

        this.fs.feedbackSubject
            .next(this.review.feedbacks.find(d => d.key === key));
        this.focusedFeedbackKey = key;
    }

    onSubmitReady() {
        setTimeout(() => {
            this.submitReady = true;
        }, 0);
    }

    submit() {
        this.submitEmitter.emit();
        this.currIndex = 0;
        this.resetSubmitReady();
        if (this.focusedFeedbackKey !== '') {
            this.focusedFeedbackKey = '';
            this.fs.resolve();
        }
    }

    onStepChange(change: StepperSelectionEvent) {
        this.currIndex = change.selectedIndex;
    }

    private resetSubmitReady() {
        if (this.config.tasks.length === 1
            && this.config.tasks[0].submitReady) {
            this.submitReady = true;
        } else {
            this.submitReady = false;
        }
    }

    ngOnDestroy() {
        if (this.sub1) {
            this.sub1.unsubscribe();
        }

        if (this.sub2) {
            this.sub2.unsubscribe();
        }
    }
}
