import { Injectable } from '@angular/core';
import { NetworkService } from 'src/app/network.service';
import { AnnotationMeta, AnnotatedImageData } from 'src/app/common/edit-data';
import * as DEMO_DATA from './demo_bar_data.json';
import { Feedback, Review } from 'src/app/common/verification-data';

@Injectable({
    providedIn: 'root'
})
export class DemoService {

    private index = 0;
    private currMode = 'init';
    private data: any[] = [];

    constructor(private network: NetworkService) {
        this.data = DEMO_DATA['default'] as any[];
    }

    async getAnnotationElem(): Promise<[AnnotationMeta, Blob]> {
        const paths = await this.network.getPaths();
        if (this.index >= paths.length) {
            return null;
        } else {
            this.currMode = this.getNextMode();
            const path = paths[this.index];
            const elem = this.data[this.index];
            const meta = { path, feedbacks: null, annotation: null };
            const blob = await this.network.getImage(meta.path);
            if (this.currMode === 'revision') {
                meta.feedbacks = elem.feedbacks.map((d: any) => new Feedback(d));
                meta.annotation = elem.annotation;
            }
            return [new AnnotationMeta(meta), blob];
        }
    }

    async getVerificationElem(): Promise<[AnnotatedImageData, Blob]> {
        const paths = await this.network.getPaths();
        if (this.index >= paths.length) {
            return null;
        } else {
            const path = paths[this.index];
            this.currMode = this.getNextMode();
            const blob = await this.network.getImage(path);
            const annotatedMeta = new AnnotatedImageData({
                path,
                annotation: this.data[this.index].annotation
            });
            return [annotatedMeta, blob];
        }
    }

    async getReview(path: string): Promise<Review> {
        const reviewRaw = {
            path,
            approved: false,
            feedbacks: this.data[this.index].feedbacks
        };
        return new Review(reviewRaw);
    }

    private getNextMode(): string {
        switch (this.currMode) {
            case 'init':
                return 'annotation';
            case 'annotation':
                return 'verification';
            case 'revision':
                this.index += 1;
                return 'annotation';
            case 'verification':
                return 'revision';
        }
    }
}
