import { Task, TASK_OVERALL } from './task';
import { FormGroup, FormBuilder, FormControl, FormArray } from '@angular/forms';

import * as d3 from 'd3';
import { VerificationData, VOverallData, TextAnnotation } from '../verification-data';

export interface BoxData {
    key?: string;
    x0: number;
    y0: number;
    x1: number;
    y1: number;
    label?: string;
    text?: string;
}

export const KEY_TITLE = 'title';
export const KEY_X_AXIS_TITLE = 'x-axis-title';
export const KEY_Y_AXIS_TITLE = 'y-axis-title';
export const KEY_X_AXIS_LABEL = 'x-axis-label';
export const KEY_Y_AXIS_LABEL = 'y-axis-label';
export const KEY_LEGEND_TITLE = 'legend-title';
export const KEY_LEGEND_LABEL = 'legend-label';
export const KEY_MARK_LABEL = 'mark-label';
export const LABEL_PRIORITY = {
    [KEY_TITLE]: 0,
    [KEY_LEGEND_TITLE]: 1,
    [KEY_LEGEND_LABEL]: 2,
    [KEY_Y_AXIS_TITLE]: 3,
    [KEY_Y_AXIS_LABEL]: 4,
    [KEY_X_AXIS_TITLE]: 5,
    [KEY_X_AXIS_LABEL]: 6,
    [KEY_MARK_LABEL]: 7,
};
export class Overall extends Task {

    public textLabels: string[];
    public features: string[];
    public collectContent: boolean;
    public inputType: 'rect' | 'point';
    public prediction: BoxData[] = [];

    constructor(raw: any) {
        super(raw);
        this.taskType = TASK_OVERALL;
        this.textLabels = [
            KEY_Y_AXIS_LABEL,
            KEY_X_AXIS_LABEL,
            KEY_LEGEND_LABEL,
            KEY_MARK_LABEL,
            KEY_TITLE,
            KEY_Y_AXIS_TITLE,
            KEY_X_AXIS_TITLE,
            KEY_LEGEND_TITLE
        ];
        this.features = ['x0', 'y0', 'x1', 'y1'];
        this.inputType = raw['input_type'];
        this.submitReady = false;
    }

    static legendDefaultForm(fb: FormBuilder): FormGroup {
        return fb.group({
            title: fb.control(''),
            labels: fb.array([])
        });
    }

    registerControl(formGroup: FormGroup, fb: FormBuilder) {
        formGroup.removeControl(this.key);
        const container = fb.group({});
        container.addControl('boxes', fb.group({}));
        container.addControl('spec', fb.group({
            title: fb.control(''),
            axis: fb.array([]),
            custom: fb.array([])
        }));
        container.addControl('raw', fb.array([]));
        formGroup.addControl(this.key, container);
    }

    getDefaultSpecForm(fb: FormBuilder) {
        return fb.group({
            title: fb.control(''),
            axis: fb.array([]),
            custom: fb.array([])
        });
    }

    updateBox(formGroup: FormGroup, raw: any) {

        const group = formGroup.get(this.key).get('boxes') as FormGroup;
        const num = (value: number) => +value.toFixed(3);

        const center = (box: any) => {
            return [
                (num(box['x0']) + num(box['x1'])) / 2,
                (num(box['y0']) + num(box['y1'])) / 2
            ];
        };
        const findAdjLabel = () => {

            const targetCenter = center(raw);
            const texts = Object.values(group.controls)
                .filter((d: FormGroup) => d.contains('text'));

            if (texts.length === 0) {
                return this.textLabels[0];
            }

            const dists = texts
                .map(d => {
                    const other = center(d.value);
                    return Math.pow(targetCenter[0] - other[0], 2)
                        + Math.pow(targetCenter[1] - other[1], 2);
                });

            const minIndex = dists.indexOf(d3.min(dists));
            return texts[minIndex].get('label').value;
        };

        if (!group.contains(raw.key)) {
            const newControl = this.features.reduce((acc, curr) => {
                acc[curr] = new FormControl(num(raw[curr]));
                return acc;
            }, {});

            if (raw.text !== undefined) {
                newControl['text'] = new FormControl(raw.text || '');
                newControl['label'] = new FormControl(raw.label || findAdjLabel());
            } else {
                newControl['label'] = new FormControl('');
            }
            group.addControl(raw.key, new FormGroup(newControl));
        } else {
            this.features.forEach(feature => {
                if (raw[feature]) {
                    group.get(raw.key).get(feature).setValue(num(raw[feature]));
                }
            });
        }
    }

    private convertToBox(raw: any[]) {
        return raw.map(d => {
            const [ x0, y0, x1, y1 ] = d['bound'];
            const label = d['label'];
            const text = d['text'];
            return {
                x0, y0, x1, y1,
                label,
                text
            };
        });
    }

    parsePrediction(raw: any) {
        this.prediction = this.convertToBox(raw as any[]);
    }

    parsePrevAnnotation(formGroup: FormGroup, raw: any, fb: FormBuilder) {
        this.registerControl(formGroup, fb);
        const marks = this.convertToBox(raw['marks']);
        const texts = this.convertToBox(raw['texts']);
        marks.concat(texts)
                .map((d, i) => ({...d, key: i.toString()}))
                .forEach(d => {
                    this.updateBox(formGroup, d);
                });
        const spec = raw['spec'];
        formGroup.get(`${this.key}.spec.title`).setValue(spec['title']);
        (spec['axis'] as any[]).forEach(axis => {
            (formGroup.get(`${this.key}.spec.axis`) as FormArray)
                .push(this.convertToForm(axis, fb));
        });
        const legend = spec['legend'];
        if (legend) {
            (formGroup.get(`${this.key}.spec`) as FormGroup)
                .addControl('legend', this.convertToForm(legend, fb));
        }
        (spec['custom'] as any[]).forEach(axis => {
            (formGroup.get(`${this.key}.spec.custom`) as FormArray)
                .push(this.convertToForm(axis, fb));
        });
        const rawGroup = fb.array([]);
        (raw['raw'] as any[]).forEach(d => {
            rawGroup.push(this.convertToForm(d, fb));
        });
        (formGroup.get(this.key) as FormGroup).setControl('raw', rawGroup);
    }

    parseForVerification(raw: any): VerificationData {
        return new VOverallData(this.key, raw);
    }

    applyPrediction(formGroup: FormGroup) {
        if (this.prediction) {
            const texts = this.prediction.filter(d => d.text !== undefined);
            const marks = this.prediction.filter(d => d.text === undefined)
                .filter(mark => {
                    const fp = texts.find(text => {
                        const xCenter = (mark.x0 + mark.x1) / 2;
                        const yCenter = (mark.y0 + mark.y1) / 2;
                        return text.x0 < xCenter
                            && text.x1 > xCenter
                            && text.y0 < yCenter
                            && text.y1 > yCenter;
                    });
                    return !fp;
                });
            marks.concat(texts)
                .map((d, i) => ({...d, key: i.toString()}))
                .forEach(raw => {
                    this.updateBox(formGroup, raw);
                });
        }
    }

    deleteBox(formGroup: FormGroup, key: string) {
        const group = formGroup.get(this.key).get('boxes') as FormGroup;
        group.removeControl(key);
    }

    getFormValue(formGroup: FormGroup) {
        const group = formGroup.get(this.key) as FormGroup;
        const marks = Object.values((group.get('boxes') as FormGroup).controls)
            .filter((d: FormGroup) => !d.contains('text'));

        const texts = Object.values((group.get('boxes') as FormGroup).controls)
            .filter((d: FormGroup) => d.contains('text'));

        const result = {
            marks: marks.map(d => ({
                bound: this.features.map(f => +d.get(f).value),
                label: 'object'
            })),
            texts: texts.map(d => ({
                bound: this.features.map(f => +d.get(f).value),
                label: d.get('label').value,
                text: d.get('text').value
            })).sort(textComparator),
            spec: group.get('spec').value,
            raw: group.get('raw').value
        };
        return result;
    }

    isEmptyBoxes(formGroup: FormGroup): boolean {
        const group = formGroup.get(this.key) as FormGroup;
        return Object.keys(group.controls).length === 0;
    }

    private convertToForm(elem: any, fb: FormBuilder): FormGroup {
        const formGroup = fb.group({});
        Object.entries(elem).forEach(([key, value]) => {
            if (Array.isArray(value)) {
                if ((value as any[]).length > 0) {
                    formGroup.addControl(key, fb.array(value));
                }
            } else {
                formGroup.addControl(key, fb.control(value));
            }
        });
        return formGroup;
    }

}

export function textComparator(a: TextAnnotation, b: TextAnnotation) {
    const aScore = LABEL_PRIORITY[a.label];
    const bScore = LABEL_PRIORITY[b.label];
    const aCenter = [
        (a.bound[0] + a.bound[2]) / 2,
        (a.bound[1] + a.bound[3]) / 2
    ];
    const bCenter = [
        (b.bound[0] + b.bound[2]) / 2,
        (b.bound[1] + b.bound[3]) / 2
    ];

    const comp = aScore - bScore;

    if (comp === 0) {
        if (a.label === KEY_X_AXIS_LABEL) {
            return aCenter[0] - bCenter[0];
        } else {
            return aCenter[1] - bCenter[1];
        }
    }
    return comp;
}
