import { FormGroup, FormBuilder } from '@angular/forms';
import { PredictData, AnnotatedImageData } from 'src/app/common/edit-data';

import { VerificationData } from './verification-data';
import { Task, TASK_CLASSIFICATION, TASK_REGRESSION, TASK_BBOX, TASK_TEXT, TASK_OVERALL } from './task/task';
import { Classification, Regression, BoundingBox, Overall } from './task';
import { environment } from 'src/environments/environment';

type ServiceMode = 'normal' | 'demo' | 'quiz';

export class Config {
    public key: string;
    public sharedDataset: boolean;
    public verification: boolean;
    public tasks: Task[];
    public service: ServiceMode;

    constructor(raw: any) {
        this.key = raw['key'];
        this.sharedDataset = raw['shared_dataset'] || false;
        this.verification = raw['verification'] || false;
        this.service = raw['service'];
        this.tasks = raw['tasks']
            .map((task: any) => {
                switch (task['task_type']) {
                    case TASK_CLASSIFICATION:
                        return new Classification(task);
                    case TASK_REGRESSION:
                        return new Regression(task);
                    case TASK_BBOX:
                        return new BoundingBox(task);
                    case TASK_TEXT:
                        return new Text(task);
                    case TASK_OVERALL:
                        return new Overall(task);
                }
            });
    }

    static getDemo(): Config {
        return new Config({
            key: 'demo',
            shared_dataset: true,
            verification: true,
            service: 'demo',
            tasks: [{
                key: 'key_0',
                name: 'demo',
                task_type: 'O',
                command: '[데모] 다음 막대 차트를 어노테이션 해보세요',
                msgs: [
                    '본 버전은 데모용으로서 Annotation, Verification, Revision을 순서대로 진행할 수 있습니다.',
                    '각 모드 당 2번씩 총 6장의 이미지가 데모용으로 준비되어 있습니다.'
                ],
                input_type: 'rect',
                predictionScore: {
                    Text: 0.92,
                    Mark: 0.878,
                    TextRole: 0.973
                }
            }],
        });
    }

    parsePrediction(predictData: PredictData) {
        this.tasks.forEach(task => {
            task.parsePrediction(predictData.labels[task.key]);
        });
    }

    parsePrevAnnotation(formGroup: FormGroup,
                        annotation: {[key: string]: any},
                        fb: FormBuilder) {
        this.tasks.forEach(task => {
            task.parsePrevAnnotation(formGroup, annotation[task.key], fb);
        });
    }

    parseVerificationData(data: AnnotatedImageData): VerificationData[] {
        return this.tasks.map(task => {
            return task.parseForVerification(data.annotation[task.key]);
        });
    }
}
