import { ElementRef } from '@angular/core';
import * as d3 from 'd3';

export type SA = S<any, any>;
export type SB = S<d3.BaseType, {}>;
export type S<T extends d3.BaseType, D> = d3.Selection<T, D, Element, {}>;
export type TScale = d3.ScaleTime<number, number>;
export type LScale = d3.ScaleLinear<number, number>;
export type Range = [number, number];

export interface Mergeable {
    merge(other: Mergeable): any;
}

export interface LegendInfoMap {
    [key: string]: LegendInfo;
}

export interface LegendInfo {
    attr: DataAttribute;
    color: string;
}

export interface DataAttribute {
    value: string;
    viewValue: string;
}

export interface Margin {
    left: number;
    right: number;
    top: number;
    bottom: number;
}

export interface Point {
    x: number;
    y: number;
}

export class Transform {
    totalHeight: number;
    totalWidth: number;
    height: number;
    width: number;
    left: number;
    bottom: number;
    right: number;
    top: number;

    constructor(totalWidth: number, totalHeight: number, margin: Margin) {
        this.totalWidth = totalWidth;
        this.totalHeight = totalHeight;
        this.height = this.totalHeight - margin.top - margin.bottom;
        this.width = this.totalWidth - margin.left - margin.right;
        this.left = margin.left;
        this.right = margin.right;
        this.bottom = margin.bottom;
        this.top = margin.top;
    }

    createSVG(svgRef: ElementRef): S<d3.BaseType, {}> {
        return d3.select(svgRef.nativeElement)
            .attr('width', this.totalWidth)
            .attr('height', this.totalHeight);
    }

    createG(svgRef: ElementRef): S<d3.BaseType, {}> {
        return this.createSVG(svgRef).append('g')
            .attr('transform', translate(this.left, this.top));
    }

    appendG(g: S<d3.BaseType, {}>) {
        return g.append('g')
            .attr('transform', translate(this.left, this.top));
    }

    xLinear(maxValue?: number): LScale {
        const scale = d3.scaleLinear().range([0, this.width]);
        return maxValue ? scale.domain([0, maxValue]) : scale;
    }

    yLinear(maxValue?: number): LScale {
        const scale = d3.scaleLinear().range([this.height, 0]);
        return maxValue ? scale.domain([0, maxValue]) : scale;
    }
}

export function log10TickFormat(d: number) {
    const superscript = '⁰¹²³⁴⁵⁶⁷⁸⁹';
    const power = Math.round(Math.log10(d));
    return 10 + (power + '').split('').map(c => superscript[c]).join('');
}

export function translate(x: number, y: number) {
    return `translate(${x}, ${y})`;
}

export function scale(x: number, y: number) {
    return `scale(${x}, ${y})`;
}

export function transform(sx: number, sy: number, tx: number, ty: number) {
    return `scale(${sx}, ${sy}) translate(${tx}px, ${ty}px)`;
}

export function getMinIndex<T>(arr: T[], func: (v: T, i?: number) => number): number {
    if (arr.length === 0) {
        return -1;
    }
    let minRet = -1;
    let minValue = Number.POSITIVE_INFINITY;
    arr.forEach((el, i) => {
        const comp = func(el, i);
        if (comp < minValue) {
            minValue = comp;
            minRet = i;
        }
    });
    return minRet;
}

export function getMaxIndex<T>(arr: T[], func: (v: T, i?: number) => number): number {
    if (arr.length === 0) {
        return -1;
    }
    let maxRet = -1;
    let maxValue = Number.POSITIVE_INFINITY;
    arr.forEach((el, i) => {
        const comp = func(el, i);
        if (comp > maxValue) {
            maxValue = comp;
            maxRet = i;
        }
    });
    return maxRet;
}

export function getMinElem<T>(arr: T[], func: (v: T, i?: number) => number): T {
    if (arr.length === 0) {
        return null;
    }
    let minEl = arr[0];
    let minValue = Number.POSITIVE_INFINITY;
    arr.forEach((el, i) => {
        const comp = func(el, i);
        if (comp < minValue) {
            minValue = comp;
            minEl = el;
        }
    });
    return minEl;
}

export function invertTransform(s: string) {
    const re = /scale\((\d+[.]*\d*)[,]\ *(\d+[.]*\d*)\) translate\(([-]*\d+[.]*\d*)px[,]\ *([-]*\d+[.]*\d*)px\)/gi;
    const result = re.exec(s);
    if (!result || result.length !== 5) {
        return {
            sx: 1,
            sy: 1,
            tx: 0,
            ty: 0
        };
    } else {
        const [sx, sy, tx, ty] = result.slice(1).map(d => +d);
        return { sx, sy, tx, ty };
    }
}

export function addLegends(svg: S<any, {}>, width = 500, height = 40, legendData: LegendInfoMap, margin = 15, horizontal = true) {
    let data = Object.values(legendData).slice();
    if (horizontal) {
        data = data.reverse();
    }
    const g = svg.append('g')
        .selectAll('g')
        .data(data)
        .enter().append('g');

    g.append('circle')
        .attr('r', 3)
        .attr('cx', 2)
        .attr('cy', 11)
        .attr('fill', d => d.color);

    g.append('text')
        .attr('dx', 10)
        .attr('dy', 14)
        .style('font-size', '0.8em')
        .text(d => d.attr.viewValue);

    let hOffset = 0;
    g.attr('transform', (_, i, nodes) => {
        hOffset += nodes[i].getBoundingClientRect().width;
        if (i > 0) {
            hOffset += margin;
        }
        if (!horizontal) {
            hOffset = width;
        }
        const vOffset = horizontal ? height / 2 - 10 : i * 30;
        return translate(width - hOffset, vOffset);
    });
}

export function binData<T extends Mergeable>(data: T[],
    xKey: string,
    yKey: string,
    width: number): T[] {

    const aggregateSize = Math.ceil(data.length / width);
    if (aggregateSize <= 1) {
        return data;
    }
    let aggCount = 0;
    let binnedData = [];
    let aggSizeData = [];

    let newElem: T = null;

    for (let elem of data) {

        if (aggCount == 0) {
            newElem = elem;
            aggCount += 1;
        } else if (aggCount < aggregateSize) {
            newElem = newElem.merge(elem);
            aggCount += 1;
        } else {
            newElem = newElem.merge(elem);
            aggCount += 1
            binnedData.push(newElem);
            aggSizeData.push(aggCount);
            aggCount = 0;
        }
    }

    if (aggCount != 0) {
        binnedData.push(newElem);
        aggSizeData.push(aggCount);
    }

    binnedData.forEach((data, index) => data[xKey] /= aggSizeData[index]);
    return binnedData;
}

export function rgb2Hex(rgb: number[]) {
    const [r, g, b, a = 255] = rgb;
    const toHex = (c: number) => {
        const hex = c.toString(16);
        return hex.length === 1 ? `0${hex}` : hex;
    };
    return `#${toHex(r)}${toHex(g)}${toHex(b)}${toHex(a)}`;
}

export function rgb2hsi(rgb: number[]) {
    let [r, g, b, _ = 255] = rgb;
    r /= 255;
    g /= 255;
    b /= 255;
    const i = (r + g + b) / 3;
    let s = 0, h = 0;

    if (r === g && g === b) {
        s = h = 0;
    } else {
        const w = (r - g + r - b) / Math.sqrt((r - g) * (r - g) + (r - b) * (g - b)) / 2;
        h = Math.acos(w) * 180 / Math.PI;

        if (b > g) {
            h = 360 - h;
        }
        s = 1 - Math.min(r, g, b) / i;
    }
    return [h, s, i];
}
