import { Injectable, ElementRef } from '@angular/core';
import { Feedback } from 'src/app/common/verification-data';
import { Subject, BehaviorSubject } from 'rxjs';

export interface FeedbackModalData {
    feedback: Feedback;
    left: number;
    top: number;
}

@Injectable({
    providedIn: 'root'
})
export class FeedbackService {

    public resolveSubject = new Subject<Feedback>();
    public showSubject = new Subject<FeedbackModalData>();
    public feedbackSubject = new BehaviorSubject<Feedback>(Feedback.dummy());
    constructor() { }

    showFeedback(left: number, top: number, feedback: Feedback) {
        this.showSubject.next({ left, top, feedback });
    }

    get resolveObservable() {
        return this.resolveSubject.asObservable();
    }

    get showObservable() {
        return this.showSubject.asObservable();
    }

    get feedbackObservable() {
        return this.feedbackSubject.asObservable();
    }

    resolve() {
        this.resolveSubject.next(this.feedbackSubject.getValue());
        this.feedbackSubject.next(Feedback.dummy());
    }
}
