import { Component, OnInit, Input, OnChanges, SimpleChanges, ElementRef, HostListener, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Text } from 'src/app/common/task';
import { SB, SA } from 'src/app/d3-utils';

import * as d3 from 'd3';
import { Observable } from 'rxjs';
import { EditService } from 'src/app/edit.service';
import { AnnotationBox, removeEventArea, createEventArea,
    addRectDragEvent, addMergeEvent, removeMergeLine, removeTempBox } from '../annotation-util';

@Component({
    selector: 'app-text',
    templateUrl: './text.component.html',
    styleUrls: [
        './text.component.scss',
        '../../edit-module.scss'
    ]
})
export class TextComponent implements OnInit, OnChanges {

    @Input() formGroup: FormGroup;
    @Input() task: Text;
    @Input() index: number;
    @Input() svgS: SB;
    @Input() imgRef: ElementRef;
    @Input() currIndex: number;
    @Input() event: Observable<string>;
    @Input() spacePressed: boolean;
    @Output() submitEmitter = new EventEmitter<void>();

    public focusedBoxIndex: string = null;
    public autoIncBoxIndex = 0;
    public boxes: AnnotationBox[] = [];
    public isDrawing = false;
    public isMerging = false;
    public g: SB;

    constructor(private edit: EditService, private fb: FormBuilder) { }

    ngOnInit() {
        this.event.subscribe(key => {
            this.isDrawing = false;
            this.isMerging = false;
            if (key === 'predict' && this.edit.autoCompleteSubject.getValue()[this.task.key]) {
                this.task.registerControl(this.formGroup, this.fb);
                this.task.applyPrediction(this.formGroup);
                this.updateBoxes();
                if (this.currIndex === this.index) {
                    this.createEventArea();
                }
            }
        });
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.currIndex || changes.formGroup) {
            this.updateBoxes();
            if (this.currIndex === this.index) {
                this.createEventArea();
                this.autoIncBoxIndex = d3.max(this.boxes.map(box => +box.key)) || 0;
                this.focusedBoxIndex = null;
            } else {
                removeEventArea(this);
            }
        }
    }

    @HostListener('window:keyup', ['$event'])
    onKey(event: KeyboardEvent) {
        if (document.activeElement.tagName === 'INPUT') {
            return;
        }

        if (event.key === 'Escape' && this.isMerging) {
            removeMergeLine(this);
        }

        if (event.key === 'Escape' && this.isDrawing) {
            removeTempBox(this);
        }
        if ((event.key === 'Delete' || event.key === 'd')
            && this.focusedBoxIndex) {
            this.deleteBox(this.focusedBoxIndex);
        }
    }

    deleteBox(boxIndex: string) {
        this.g.select(`.rect-${boxIndex}`).remove();
        this.g.selectAll(`.circle-${boxIndex}`).remove();
        this.task.deleteBox(this.formGroup, boxIndex);
        if (boxIndex === this.focusedBoxIndex) {
            this.focusedBoxIndex = null;
        }
        this.updateBoxes();
    }

    private createEventArea() {
        removeEventArea(this);
        createEventArea(this, true);
    }

    writeForm(boxIndex: string, x: number, y: number, width: number, height: number) {
        const imgElem = this.imgRef.nativeElement;
        const clamp = (value: number, min: number, max: number) => {
            return Math.min(max, Math.max(min, value));
        };
        const raw = {
            key: boxIndex,
            x0: clamp(x - 10, 0, imgElem.width) / imgElem.width,
            y0: clamp(y - 10, 0, imgElem.height) / imgElem.height,
            x1: clamp((x + width) - 10, 0, imgElem.width) / imgElem.width,
            y1: clamp((y + height) - 10, 0, imgElem.height) / imgElem.height,
        };
        const addNewBox = !this.formGroup
            .get(this.task.key)
            .get(raw.key);

        this.task.updateBox(this.formGroup, raw);
        if (addNewBox) {
            this.updateBoxes();
        }
    }

    addRectEvent(boxIndex: string, rectS: SB, circleS: SA) {
        addRectDragEvent(this, boxIndex, rectS, circleS);
        addMergeEvent(this, boxIndex, rectS);
    }

    updateFocus(boxIndex: string) {
        this.g.selectAll('rect').classed('focused', false);
        this.g.selectAll('circle').classed('focused', false);
        this.g.select(`.rect-${boxIndex}`).classed('focused', true);
        this.focusedBoxIndex = boxIndex;
    }

    updateBoxes() {
        const formGroup = this.formGroup.get(this.task.key) as FormGroup;
        this.boxes = Object
            .entries(formGroup.controls)
            .map(entry => {
                const [key, value] = entry;
                return new AnnotationBox(key, value as FormGroup);
            });
    }

    onSelect(event: Event) {
        event.stopPropagation();
    }
}
