import * as d3 from 'd3';
import { SB, SA, S, rgb2hsi } from 'src/app/d3-utils';
import { TextComponent } from './text/text.component';
import { BoundingBoxComponent } from './bounding-box/bounding-box.component';
import { AbstractControl, FormGroup } from '@angular/forms';
import { ContainerElement } from 'd3';
import { OverallComponent } from './overall/overall.component';
import { KEY_TITLE, KEY_X_AXIS_TITLE, KEY_X_AXIS_LABEL,
    KEY_Y_AXIS_LABEL, KEY_Y_AXIS_TITLE, KEY_LEGEND_TITLE,
    KEY_LEGEND_LABEL, KEY_MARK_LABEL, LABEL_PRIORITY } from 'src/app/common/task';
import { TextAnnotation } from 'src/app/common/verification-data';

type BC = BoundingBoxComponent;
type TC = TextComponent;
type OC = OverallComponent;

export const NUM_REG = /[m|mm|km|s|g|kg|K|M|%|$|,]/g;

export interface Handle {
    cx: number;
    cy: number;
    pivotX: number;
    pivotY: number;
}

export class AnnotationBox {
    key: string;
    points: { [key: string]: AbstractControl };
    label: AbstractControl;
    text?: AbstractControl;

    constructor(key: string, formGroup: FormGroup) {
        this.key = key;
        this.points = {
            x0: formGroup.get('x0'),
            x1: formGroup.get('x1'),
            y0: formGroup.get('y0'),
            y1: formGroup.get('y1')
        };
        this.label = formGroup.get('label');
        this.text = formGroup.get('text');
    }

    get pointValues(): number[] {
        return [
            this.points['x0'].value,
            this.points['y0'].value,
            this.points['x1'].value,
            this.points['y1'].value,
        ];
    }

    get width(): number {
        return +this.points.x1.value - +this.points.x0.value;
    }

    get height(): number {
        return +this.points.y1.value - +this.points.y0.value;
    }

    get center(): [number, number] {
        return [
            (+this.points.x0.value + +this.points.x1.value) / 2,
            (+this.points.y0.value + +this.points.y1.value) / 2
        ];
    }

    get isText() {
        return this.text !== null;
    }

    get centerDist() {
        const center = this.center;
        return center[0] * center[0] + center[1] * center[1];
    }
}

function getRectHandles(x: number, y: number, width: number, height: number): Handle[] {
    return [
        { cx: x, cy: y, pivotX: x + width, pivotY: y + height, },
        { cx: x + width, cy: y, pivotX: x, pivotY: y + height, },
        { cx: x, cy: y + height, pivotX: x + width, pivotY: y },
        { cx: x + width, cy: y + height, pivotX: x, pivotY: y }
    ];
}

export function isNumberTexts(boxes: AnnotationBox[]) {
    const numNumbers = d3.sum(boxes
        .map(d => d.text.value)
        .map(d => {
        return isNumberText(d)
        ? 1
        : 0;
    }));
    return numNumbers === boxes.length;
}

export function isNumberText(v: string) {
    return !isNaN(+(v.replace(NUM_REG, '')));
}

export function removeEventArea(comp: BC | TC | OC) {
    comp.svgS.selectAll('g').remove();
}

export function createEventArea(comp: BC | TC | OC,
                                mergeEvent: boolean,
                                inputMode = 'rect') {

    const svg = comp.svgS;
    const key = comp.task.key;
    let pivotX = 0;
    let pivotY = 0;
    comp.g = svg.append('g').attr('class', key);
    let newBoxIndex = (comp.autoIncBoxIndex + 1).toString();

    const horLine = comp.g.append('line')
        .attr('class', 'guide-horizontal')
        .attr('x1', 0)
        .attr('x2', 1000)
        .attr('y1', 0)
        .attr('y2', 0)
        .attr('stroke-width', 1)
        .attr('stroke', 'rgba(0, 0, 0, 0.3)')
        .style('pointer-events', 'none');

    const verLine = comp.g.append('line')
        .attr('class', 'guide-vertical')
        .attr('y1', 0)
        .attr('y2', 1000)
        .attr('x1', 0)
        .attr('x2', 0)
        .attr('stroke-width', 1)
        .attr('stroke', 'rgba(0, 0, 0, 0.3)')
        .style('pointer-events', 'none');

    const initRect = () => {
        newBoxIndex = (comp.autoIncBoxIndex + 1).toString();
        const x = d3.event.offsetX;
        const y = d3.event.offsetY;

        comp.g.append('rect')
            .attr('class', `rect-${newBoxIndex}`)
            .attr('x', x).attr('y', y)
            .attr('stroke-width', 2)
            .attr('stroke', 'red')
            .attr('fill', 'rgba(255, 0, 0, 0)');

        pivotX = x;
        pivotY = y;
        comp.isDrawing = true;
    };

    const drawRect = () => {
        const eventX = d3.event.offsetX;
        const eventY = d3.event.offsetY;
        const x = eventX > pivotX ? pivotX : eventX;
        const y = eventY > pivotY ? pivotY : eventY;
        const width = Math.abs(eventX - pivotX);
        const height = Math.abs(eventY - pivotY);

        svg.select(`.${key}`).select(`.rect-${newBoxIndex}`)
            .attr('x', x)
            .attr('y', y)
            .attr('width', width)
            .attr('height', height);

        comp.writeForm(newBoxIndex, x, y, width, height);
    };

    const drawPoint = () => {
        newBoxIndex = (comp.autoIncBoxIndex + 1).toString();
        const x = d3.event.offsetX;
        const y = d3.event.offsetY;
        const r = (comp as OC).pointRadius;

        const circleS = comp.g.append('circle')
            .attr('class', `rect-${newBoxIndex}`)
            .attr('cx', x).attr('cy', y)
            .attr('r', r)
            .attr('fill-opacity', 0)
            .attr('stroke-width', 2)
            .attr('stroke', 'darkorange');

        comp.writeForm(newBoxIndex, x - r, y - r, 2 * r, 2 * r);
        comp.updateFocus(newBoxIndex);
        addCircleDragEvent(comp as OC, newBoxIndex, circleS);
    };

    const drawCircles = () => {
        const g = svg.select(`.${key}`);
        const rectS = g.select(`.rect-${newBoxIndex}`);
        const x = +rectS.attr('x');
        const y = +rectS.attr('y');
        const width = +rectS.attr('width');
        const height = +rectS.attr('height');
        const handles = getRectHandles(x, y, width, height);
        const circleS = g.selectAll(`.circle-${newBoxIndex}`)
            .data(handles).enter()
            .append('circle').attr('class', `circle-${newBoxIndex} handle`)
            .classed('focused', comp.focusedBoxIndex === newBoxIndex)
            .attr('cx', d => d.cx)
            .attr('cy', d => d.cy)
            .attr('r', 3).attr('fill', 'darkorange');

        comp.updateFocus(newBoxIndex);
        addCircleEvent(comp, newBoxIndex, rectS, circleS as SA);
        comp.addRectEvent(newBoxIndex, rectS, circleS as SA);
        comp.isDrawing = false;
    };

    const updateLine = () => {
        comp.g.select('#merge-line')
            .attr('x2', d3.event.offsetX)
            .attr('y2', d3.event.offsetY)
            .attr('stroke-width', 3)
            .attr('stroke', 'rgb(51, 153, 102)')
            .attr('stroke-opacity', 0.6);
    };

    svg.on('click', () => {
        if (comp.spacePressed || (comp as OC).stage !== 0) {
            return;
        }
        if (!comp.isDrawing && comp.index === comp.currIndex) {
            if (inputMode === 'point'
                       && (comp as OC).boxMode === 'mark') {
                drawPoint();
                comp.autoIncBoxIndex = +newBoxIndex;
            } else {
                initRect();
            }
        } else if (comp.isDrawing) {
            drawCircles();
            comp.autoIncBoxIndex = +newBoxIndex;
        }
    }).on('mouseenter', () => {
        horLine.attr('visibility', 'visible');
        verLine.attr('visibility', 'visible');
    }).on('mousemove', () => {
        if (comp.isDrawing) {
            drawRect();
        }

        if (mergeEvent && (comp as TC).isMerging) {
            updateLine();
        }
        moveGuides(horLine, verLine, d3.event.offsetX, d3.event.offsetY);
    }).on('mouseleave', () => {
        horLine.attr('visibility', 'hidden');
        verLine.attr('visibility', 'hidden');
    });

    if (comp.boxes.length > 0) {
        comp.isDrawing = false;
        if (mergeEvent) {
            (comp as TC).isMerging = false;
        }
        comp.autoIncBoxIndex = comp.boxes.length;
        comp.boxes.forEach(box => {
            if ((comp as OC).task.inputType === 'point'
                && box.text === null) {
                drawPointMark(comp as OC, box);
            } else {
                drawBoxMark(comp, box);
            }
        });
    }
}

export function removeTempBox(comp: BC | TC | OC) {
    const tempIndex = (comp.autoIncBoxIndex + 1).toString();
    comp.deleteBox(tempIndex);
    comp.isDrawing = false;
}

export function removeMergeLine(comp: TC | OC) {
    comp.g.select('#merge-line').remove();
    comp.isMerging = false;
}

export function drawBoxMark(comp: BC | TC | OC, box: AnnotationBox) {
    const imgElem = comp.imgRef.nativeElement as HTMLImageElement;
    const [x0, y0, x1, y1] = box.pointValues;
    const boxIndex = box.key;
    const x = x0 * imgElem.width + 10;
    const y = y0 * imgElem.height + 10;
    const width = (x1 - x0) * imgElem.width;
    const height = (y1 - y0) * imgElem.height;
    const rectS = comp.g.append('rect')
        .attr('class', `rect-${boxIndex}`)
        .classed('focused', comp.focusedBoxIndex === boxIndex)
        .attr('x', x)
        .attr('y', y)
        .attr('width', width)
        .attr('height', height)
        .attr('stroke-width', 2)
        .attr('stroke', 'red')
        .attr('fill', 'rgba(255, 0, 0, 0)');

    const handles = getRectHandles(x, y, width, height);
    const circleS = comp.g.selectAll(`.circle-${boxIndex}`)
        .data(handles).enter()
        .append('circle')
        .attr('class', `circle-${boxIndex} handle`)
        .classed('focused', comp.focusedBoxIndex === boxIndex)
        .attr('cx', (d: Handle) => d.cx)
        .attr('cy', (d: Handle) => d.cy)
        .attr('r', 3).attr('fill', 'darkorange');

    comp.addRectEvent(boxIndex, rectS, circleS as SA);
    addCircleEvent(comp, boxIndex, rectS, circleS as SA);
}

export function drawPointMark(comp: OC, box: AnnotationBox) {
    const r = comp.pointRadius;
    const imgElem = comp.imgRef.nativeElement as HTMLImageElement;
    const [x0, y0, x1, y1] = box.pointValues;
    const boxIndex = box.key;
    const x = (x0 + x1) * imgElem.width / 2 + 10;
    const y = (y0 + y1) * imgElem.height / 2 + 10;
    const circleS = comp.g.append('circle')
        .attr('class', `rect-${boxIndex}`)
        .classed('focused', comp.focusedBoxIndex === boxIndex)
        .attr('cx', x).attr('cy', y)
        .attr('r', r)
        .attr('fill-opacity', 0)
        .attr('stroke-width', 2)
        .attr('stroke', 'darkorange');

    addCircleDragEvent(comp as OC, boxIndex, circleS);
}

export function addRectDragEvent(comp: BC | TC | OC,
    boxIndex: string, rectS: SB, circleS: SA) {

    let dragStartX = 0;
    let dragStartY = 0;
    let pivotX = 0;
    let pivotY = 0;
    let width = +rectS.attr('width');
    let height = +rectS.attr('height');
    const horLine = comp.g.select('.guide-horizontal');
    const verLine = comp.g.select('.guide-vertical');

    rectS.on('click', () => {
        if (comp.spacePressed) {
            return;
        }
        comp.updateFocus(boxIndex);
        d3.event.stopPropagation();
        return false;
    }).on('contextmenu', () => {
        d3.event.preventDefault();
        d3.event.stopPropagation();
        if ((comp as OC).isMerging) {
            removeMergeLine(comp as OC);
        }
    });

    rectS.call(d3.drag()
        .on('start', () => {

            if (comp.spacePressed || (comp as OC).stage !== 0) {
                return;
            }
            dragStartX = d3.event.x;
            dragStartY = d3.event.y;
            pivotX = +rectS.attr('x');
            pivotY = +rectS.attr('y');
            width = +rectS.attr('width');
            height = +rectS.attr('height');
            comp.updateFocus(boxIndex);
        })
        .on('drag', () => {
            if (comp.spacePressed || (comp as OC).stage !== 0) {
                return;
            }
            const eventX = d3.event.x;
            const eventY = d3.event.y;
            const moveX = eventX - dragStartX;
            const moveY = eventY - dragStartY;
            const x = pivotX + moveX;
            const y = pivotY + moveY;

            rectS.attr('x', x).attr('y', y);
            moveGuides(horLine, verLine, eventX, eventY);

            const points = getRectHandles(x, y, width, height);
            circleS.data(points)
                .attr('cx', d => d.cx)
                .attr('cy', d => d.cy);

            comp.writeForm(boxIndex, x, y, width, height);
        }));
}

function addCircleEvent(comp: BC | TC | OC, boxIndex: string, rectS: SB, circleS: SA) {
    const horLine = comp.g.select('.guide-horizontal');
    const verLine = comp.g.select('.guide-vertical');
    circleS.call(d3.drag()
        .on('start', () => {

            if (comp.spacePressed || (comp as OC).stage !== 0) {
                return;
            }
            comp.updateFocus(boxIndex);
        })
        .on('drag', (d: Handle) => {

            if (comp.spacePressed || (comp as OC).stage !== 0) {
                return;
            }

            const pivotX = d.pivotX;
            const pivotY = d.pivotY;
            const eventX = d3.event.x;
            const eventY = d3.event.y;
            const x = eventX > pivotX ? pivotX : eventX;
            const y = eventY > pivotY ? pivotY : eventY;
            const width = Math.abs(eventX - pivotX);
            const height = Math.abs(eventY - pivotY);
            moveGuides(horLine, verLine, eventX, eventY);

            rectS
                .attr('x', x)
                .attr('y', y)
                .attr('width', width)
                .attr('height', height);

            const handles = getRectHandles(x, y, width, height);

            circleS.data(handles)
                .attr('cx', (handle: Handle) => handle.cx)
                .attr('cy', (handle: Handle) => handle.cy);

            comp.writeForm(boxIndex, x, y, width, height);
    }));
}

function addCircleDragEvent(comp: OC, boxIndex: string, circleS: SA) {
    const horLine = comp.g.select('.guide-horizontal');
    const verLine = comp.g.select('.guide-vertical');
    const r = (comp as OC).pointRadius;

    circleS.on('click', () => {
        if (comp.spacePressed) {
            return;
        }
        comp.updateFocus(boxIndex);
        d3.event.stopPropagation();
        return false;
    });

    circleS.call(d3.drag()
        .on('start', () => {
            if (comp.spacePressed || (comp as OC).stage !== 0) {
                return;
            }
            comp.updateFocus(boxIndex);
        }).on('drag', (d: Handle) => {

            if (comp.spacePressed || (comp as OC).stage !== 0) {
                return;
            }
            const x = d3.event.x;
            const y = d3.event.y;
            moveGuides(horLine, verLine, x, y);
            circleS.attr('cx', x).attr('cy', y);
            comp.writeForm(boxIndex, x - r, y - r, 2 * r, 2 * r);
        })
    );
}

export function addMergeEvent(comp: TC | OC, boxIndex: string, rectS: SB) {
    const img = comp.imgRef.nativeElement as HTMLImageElement;
    const imgWidth = img.width;
    const imgHeight = img.height;

    const initLine = () => {
        const initX = d3.event.offsetX;
        const initY = d3.event.offsetY;
        comp.g.append('line')
            .attr('id', 'merge-line')
            .attr('x1', initX)
            .attr('y1', initY)
            .attr('x2', initX)
            .attr('y2', initY)
            .attr('stroke-width', 5)
            .attr('stroke', 'gray')
            .style('pointer-events', 'none');
    };

    const merge = () => {
        const line = comp.g.select('#merge-line');
        const x1 = +line.attr('x1');
        const x2 = +line.attr('x2');
        const y1 = +line.attr('y1');
        const y2 = +line.attr('y2');
        const points = d3.range(0, 1.1, 0.04).map(t => {
            return {
                x: x1 + (x2 - x1) * t,
                y: y1 + (y2 - y1) * t
            };
        });
        let mergedBoxes = comp.boxes
            .filter((box: AnnotationBox) => box.text !== null)
            .filter((box: AnnotationBox) => {
            const rx0 = box.points.x0.value * imgWidth + 10;
            const ry0 = box.points.y0.value * imgHeight + 10;
            const rx1 = box.points.x1.value * imgWidth + 10;
            const ry1 = box.points.y1.value * imgHeight + 10;

            return points.findIndex(p => {
                return p.x >= rx0
                    && p.x <= rx1
                    && p.y >= ry0
                    && p.y <= ry1;
            }) !== -1;

        });
        if (mergedBoxes.length === 0) {
            return;
        }
        const labelCounter: {[label: string]: number} = {};
        mergedBoxes.forEach(box => {
            const label = box.label.value;
            const count = labelCounter[label];
            if (count === undefined) {
                labelCounter[label] = 1;
            } else {
                labelCounter[label] += 1;
            }
        });

        const maxLabel = Object.entries(labelCounter).sort((a, b) => b[1] - a[1])[0][0];

        const xMin = d3.min(mergedBoxes, d => d.points.x0.value);
        const xMax = d3.max(mergedBoxes, d => d.points.x1.value);
        const yMin = d3.min(mergedBoxes, d => d.points.y0.value);
        const yMax = d3.max(mergedBoxes, d => d.points.y1.value);

        const alignHorizontal = 1.5 * (xMax - xMin) > (yMax - yMin);
        const lineTan = Math.atan2(y2 - y1, x2 - x1);
        const perpendicularMerge = alignHorizontal
            ? Math.abs(lineTan) > 1
            : Math.abs(lineTan) <= 1;
        mergedBoxes = mergedBoxes.sort((a, b) => {
            if (alignHorizontal) {
                return perpendicularMerge
                    ? a.points.y0.value - b.points.y0.value
                    : a.points.x0.value - b.points.x0.value;
            } else {
                return perpendicularMerge
                    ? a.points.x0.value - b.points.x0.value
                    : b.points.y0.value - a.points.y0.value;
            }
        });
        const mergedText = mergedBoxes.map(d => d.text.value).join(' ');
        mergedBoxes.forEach(d => {
            const deletedBoxIndex = d.key;
            if (comp.focusedBoxIndex === boxIndex) {
                comp.focusedBoxIndex = null;
            }

            comp.g.select(`.rect-${deletedBoxIndex}`).remove();
            comp.g.selectAll(`.circle-${deletedBoxIndex}`).remove();
            comp.task.deleteBox(comp.formGroup, deletedBoxIndex);
        });
        const newBoxIndex = (comp.autoIncBoxIndex + 1).toString();
        const raw = {
            key: newBoxIndex,
            x0: xMin,
            x1: xMax,
            y0: yMin,
            y1: yMax,
            label: maxLabel,
            text: mergedText
        };
        comp.autoIncBoxIndex += 1;
        comp.task.updateBox(comp.formGroup, raw);
        comp.updateBoxes();
        drawBoxMark(comp, comp.boxes.find(d => d.key === newBoxIndex));
        comp.updateFocus(newBoxIndex);
    };

    rectS.on('contextmenu', () => {
        d3.event.preventDefault();
        d3.event.stopPropagation();

        if (comp.spacePressed || (comp as OC).stage !== 0) {
            return;
        }

        if (!comp.isMerging && comp.index === comp.currIndex) {
            initLine();
        } else if (comp.isMerging) {
            merge();
            comp.g.select('#merge-line').remove();
        }
        comp.isMerging = !comp.isMerging;
    });
}

export function getImageData(imgElem: HTMLImageElement) {
    const canvas = document.createElement('canvas');
    const context = canvas.getContext('2d');
    const { width, height } = imgElem;
    canvas.height = height;
    canvas.width = width;
    context.drawImage(imgElem, 0, 0, width, height);
    return Array.from(context.getImageData(0, 0, width, height).data);
}

export function drawBoxBySelectedPixel(comp: BC | OC) {
    d3.event.preventDefault();
    const container = comp.svgS as S<ContainerElement, {}>;
    const [x, y] = d3.mouse(container.node()).map(d => Math.floor(d - 10));
    const imgElem = comp.imgRef.nativeElement as HTMLImageElement;
    const { width, height } = imgElem;
    const imgData = getImageData(imgElem);
    drawBoxByPoint(comp, imgData, width, height, x, y);
}

export function regressBoxesByColor(comp: BC | OC) {
    const imgElem = comp.imgRef.nativeElement as HTMLImageElement;
    const { width, height } = imgElem;
    const marks = comp.boxes.filter(box => box.text === null);
    const imgData = getImageData(imgElem);
    const markCenters = marks.map(mark => {
        return [
            Math.round(mark.center[0] * width),
            Math.round(mark.center[1] * height)
        ];
    });
    marks.forEach(mark => {
        comp.removeBox(mark.key);
    });
    comp.updateBoxes();
    markCenters.forEach(center => {
        const [x, y] = center;
        drawBoxByPoint(comp, imgData, width, height, x, y);
    });
}

export function regressArea(imgData: number[], width: number, height: number, x: number, y: number): any[] {
    interface ColorValue {
        type: string;
        r?: number;
        g?: number;
        b?: number;
        h?: number;
        s?: number;
        i?: number;
    }

    const color = (p: number[]): ColorValue => {
        const [px, py] = p;
        const start = (py * width + px) * 4;
        const rgb = imgData.slice(start, start + 4);
        const hsi = rgb2hsi(rgb).slice(0, 3);
        const result = { type: 'rgb', r: rgb[0], g: rgb[1], b: rgb[2] };
        if (hsi[0] > 2) {
            result.type = 'hsi';
            return { ...result, h: hsi[0], s: hsi[1], i: hsi[2] };
        } else {
            return result;
        }
    };

    const point = (index: number) => {
        return [index % width, Math.floor(index / width)];
    };

    const isSameColor = (a: ColorValue, b: ColorValue) => {
        const sameRGB = Math.abs(a.r - b.r) < 10
            && Math.abs(a.g - b.g) < 10
            && Math.abs(a.b - b.b) < 10;
        if (a.type === 'hsi' && b.type === 'hsi') {
            return Math.abs(a.h - b.h) < 5
                && Math.abs(a.s - b.s) < 0.15
                && sameRGB;
        } else {
            return sameRGB;
        }
    };

    const startColor = color([x, y]);
    const startIndex = y * width + x;
    const marks = new Array(width * height).fill(false);
    const queue = [];
    const points = [];
    queue.push(startIndex);
    marks[startIndex] = true;
    points.push({x, y});
    const directions = [[-1, 0], [1, 0], [0, 1], [0, -1]];
    while (queue.length > 0) {
        const currIndex = queue[0];
        queue.shift();
        const [px, py] = point(currIndex);
        directions.forEach(dir => {
            const nextPoint = [px + dir[0], py + dir[1]];
            const [npx, npy] = nextPoint;
            const nextIndex = npy * width + npx;
            if (npx < 0 || npx >= width || npy < 0 || npy >= height) {
                return;
            }
            const notMarked = !marks[nextIndex];
            if (notMarked && isSameColor(startColor, color([npx, npy]))) {
                queue.push(nextIndex);
                marks[nextIndex] = true;
                points.push({x: npx, y: npy});
            }

        });
    }
    return points;
}

export function drawBoxByPoint(comp: BC | OC,
    imgData: number[], width: number, height: number, x: number, y: number) {
    const points = regressArea(imgData, width, height, x, y);
    const x0 = Math.max(0, d3.min(points, d => d.x) - 1) + 10;
    const y0 = Math.max(0, d3.min(points, d => d.y) - 1) + 10;
    const x1 = Math.min(d3.max(points, d => d.x) + 1, width) + 10;
    const y1 = Math.min(d3.max(points, d => d.y) + 1, height) + 10;

    if ((y1 - y0) * (x1 - x0) > width * height * 0.9) {
        return;
    }
    const boxIndex = (comp.autoIncBoxIndex + 1).toString();
    comp.autoIncBoxIndex += 1;
    comp.writeForm(boxIndex, x0, y0, x1 - x0, y1 - y0);
    const box = new AnnotationBox(boxIndex, comp.box(boxIndex));
    drawBoxMark(comp, box);
    comp.updateFocus(boxIndex);
}

export function boxComparator(a: AnnotationBox, b: AnnotationBox) {
    if (a.isText && b.isText) {
        const aLabel = a.label.value as string;
        const bLabel = b.label.value as string;
        const comp = LABEL_PRIORITY[aLabel] - LABEL_PRIORITY[bLabel];

        if (comp === 0) {
            if (aLabel === KEY_X_AXIS_LABEL) {
                return a.center[0] - b.center[0];
            } else {
                return a.center[1] - b.center[1];
            }
        }

        return comp;
    } else if (a.isText) {
        return -1;
    } else if (b.isText) {
        return 1;
    } else {
        return 0;
    }
}


export function moveGuides(horLine: SA, verLine: SA, x: number, y: number) {
    horLine.attr('y1', y).attr('y2', y);
    verLine.attr('x1', x).attr('x2', x);
}
