import { Component, OnInit, Input, Output, EventEmitter, AfterViewInit, OnDestroy, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormArray, FormControl } from '@angular/forms';
import { AnnotationBox, isNumberText } from '../../annotation-util';
import { KEY_MARK_LABEL, Overall } from 'src/app/common/task';
import { MatCheckboxChange, MatSelectChange } from '@angular/material';
import { Subscription } from 'rxjs';
import { FeedbackService } from 'src/app/edit/common/feedback.service';
import { Feedback } from 'src/app/common/verification-data';

@Component({
    selector: 'app-custom-spec',
    templateUrl: './custom-spec.component.html',
    styleUrls: [
        './custom-spec.component.scss',
        '../../../edit-module.scss',
        '../overall.component.scss'
    ]
})
export class CustomSpecComponent implements OnInit, AfterViewInit, OnDestroy {

    @Input() index: number;
    @Input() group: FormGroup;
    @Input() task: Overall;
    @Input() texts: AnnotationBox[];

    @Output() closed = new EventEmitter<number>();
    @Output() scroll = new EventEmitter<number>();

    @ViewChild('titleRef', { static: false }) titleRef: ElementRef;
    @ViewChild('type', { static: false }) typeRef: ElementRef;
    @ViewChild('unit', { static: false }) unitRef: ElementRef;
    @ViewChild('markLabels', { static: false }) markLabelsRef: ElementRef;

    public markLabels: AnnotationBox[] = [];
    public focusedFeedbackKey = '';
    public focusedFeedback: Feedback;
    private refMap: { [key: string]: ElementRef };

    private sub: Subscription;

    constructor(private fs: FeedbackService) {

    }

    ngOnInit() {
        this.sub = this.fs.feedbackObservable.subscribe(feedback => {
            this.focusedFeedbackKey = '';
            this.focusedFeedback = null;
            if (feedback.taskKey !== this.task.key) {
                return;
            }

            const splited = feedback.annotationKey.split('.');
            if (splited[0] !== 'spec' ||
                splited[1] !== 'custom' ||
                +splited[2] !== this.index) {
                    return;
            }

            this.focusedFeedbackKey = splited[3];
            this.focusedFeedback = feedback;
            setTimeout(() => {
                this.scrollEvent();
            }, 0);
        });

        this.markLabels = this.texts
            .filter(d => d.label.value === KEY_MARK_LABEL);

        const labelForms = (this.group.get('markLabels') as FormArray);
        if (labelForms.length === 0) {
            this.resetMarkLabels();
        }
    }

    ngAfterViewInit() {
        this.refMap = {
            title: this.titleRef,
            type: this.typeRef,
            unit: this.unitRef,
            markLabels: this.markLabelsRef
        };
    }

    close() {
        this.closed.emit(this.index);
    }

    checkLabeledByMarks(_: MatCheckboxChange) {
        this.resetMarkLabels();
    }

    private resetMarkLabels() {
        const labelForms = (this.group.get('markLabels') as FormArray);
        if (!this.labeledByMarks) {
            while (labelForms.length) {
                labelForms.removeAt(0);
            }
            return;
        }
        this.markLabels.forEach(label => {
            if (this.valueType === 'nominal' || isNumberText(label.text.value)) {
                labelForms.push(new FormControl(label.text.value));
            }
        });
    }

    onValueTypeChange(change: MatSelectChange) {
        if (change.value === 'quantitative') {
            this.group.get('unit').setValue('integer');
        } else {
            this.group.get('unit').setValue('');
        }
    }

    scrollEvent() {
        const ref = this.refMap[this.focusedFeedbackKey];
        if (!ref) {
            return;
        }
        const el = ref.nativeElement as HTMLElement;
        const top = el.getBoundingClientRect().top;
        this.scroll.emit(top);
    }

    focusedElemWithKey(key: string): boolean {
        return !!this.focusedFeedback && this.focusedFeedbackKey === key;
    }

    ngOnDestroy() {
        if (this.sub) {
            this.sub.unsubscribe();
        }
    }

    get hasMarkLabels() {
        return this.markLabels.length > 0;
    }

    get title() {
        return `Custom Column ${this.index + 1}`;
    }

    get valueType() {
        return this.group.get('type').value;
    }

    get labeledByMarks() {
        return this.group.get('labeled').value;
    }
}
