import { Component, OnInit, ViewChild, ElementRef, Input, OnChanges, SimpleChanges } from '@angular/core';

@Component({
    selector: 'app-canvas-patch',
    templateUrl: './canvas-patch.component.html',
    styleUrls: ['./canvas-patch.component.scss']
})
export class CanvasPatchComponent implements OnInit, OnChanges {

    @Input() data: number[][];
    @ViewChild('canvas', { static: true }) canvas: ElementRef;
    constructor() { }

    ngOnInit() {

    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.data && changes.data.currentValue) {
            const data = changes.data.currentValue as number[][];
            if (data.length === 0) {
                return;
            }
            const el = this.canvas.nativeElement as HTMLCanvasElement;
            const ctx = el.getContext('2d');
            const imgData = ctx.createImageData(15, 15);
            data.forEach((pixel, i) => {
                for (let j = 0; j < 3; j++) {
                    imgData.data[i * 4 + j] = pixel[j];
                }
                imgData.data[i * 4 + 3] = 255;
            });
            ctx.putImageData(imgData, 0, 0);
        }
    }

}
