import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Regression } from 'src/app/common/task';
import { Observable } from 'rxjs';
import { EditService } from 'src/app/edit.service';

@Component({
    selector: 'app-regression',
    templateUrl: './regression.component.html',
    styleUrls: [
        './regression.component.scss',
        '../../edit-module.scss',
    ]
})
export class RegressionComponent implements OnInit {

    @Input() formGroup: FormGroup;
    @Input() task: Regression;
    @Input() event: Observable<string>;
    @Input() spacePressed: boolean;
    @Output() submitEmitter = new EventEmitter<void>();

    constructor(private edit: EditService) { }

    ngOnInit() {
        this.event.subscribe(key => {
            if (key === 'predict' && this.edit.autoCompleteSubject.getValue()[this.task.key]) {
                this.task.applyPrediction(this.formGroup);
            }
        });
    }

    getControl(key: string) {
        return this.formGroup.get(this.task.key).get(key);
    }

}
