import { Component, OnInit, Input, Output, EventEmitter, ElementRef } from '@angular/core';
import { VerificationData, Feedback } from 'src/app/common/verification-data';
import { Config } from 'src/app/common/config';
import { NetworkService } from 'src/app/network.service';
import { EditService } from 'src/app/edit.service';
import { SA } from 'src/app/d3-utils';
import { FormGroup, FormBuilder } from '@angular/forms';

@Component({
    selector: 'app-verification-module',
    templateUrl: './verification-module.component.html',
    styleUrls: ['./verification-module.component.scss']
})
export class VerificationModuleComponent implements OnInit {

    @Input() config: Config;
    @Input() verificationDatas: VerificationData[];
    @Input() onImageLoad: boolean;
    @Input() imgRef: ElementRef;
    @Input() svgS: SA;

    @Output() reviewEmitter = new EventEmitter<void>();

    public feedbackForms: FormGroup[] = [];
    public focusedKey = null;

    constructor(private network: NetworkService,
                private edit: EditService,
                private fb: FormBuilder) { }

    ngOnInit() {

    }

    async sendReview(approved: boolean) {
        const feedbacks = this.feedbackForms.map(d => Feedback.from(d));
        const path = this.edit.image.path;
        if (this.config.service === 'normal') {
            await this.network.registerReview({ path, approved, feedbacks });
        }
        this.reviewEmitter.emit();
    }

    onAddFeedback(form: FormGroup) {
        const index = this.verificationDatas.findIndex(d =>
            d.taskKey === form.get('taskKey').value);
        form.addControl('index', this.fb.control(index));
        this.feedbackForms.push(form);
        this.feedbackForms = [].concat(this.feedbackForms);
        const { taskKey, annotationKey } = form.value;
        this.focusedKey = `${taskKey}.${annotationKey}`;
    }

    onRemoveFeedback(form: FormGroup) {
        const index = this.feedbackForms.findIndex(f => f === form);
        if (index !== -1) {
            this.feedbackForms.splice(index, 1);
            this.feedbackForms = [].concat(this.feedbackForms);
        }
    }
}
