import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { AxisAnnotation, KV } from 'src/app/common/verification-data';

@Component({
    selector: 'app-axis-spec-card',
    templateUrl: './axis-spec-card.component.html',
    styleUrls: [
        './axis-spec-card.component.scss',
        '../v-overall.component.scss'
    ]
})
export class AxisSpecCardComponent implements OnInit {

    @Input() data: AxisAnnotation;
    @Input() index: number;
    @Input() focusedKey: string;
    @Output() focus = new EventEmitter<string>();

    constructor() { }

    ngOnInit() { }

    emit(el: KV<any>) {
        this.focus.emit(el.key);
    }

    get labels(): string {
        if (this.data && this.data.labels.value.length > 0) {
            return this.data.labels.value.join(', ');
        } else {
            return '';
        }
    }

    get markLabels(): string {
        if (this.data &&
            this.data.markLabels &&
            this.data.markLabels.value.length > 0) {
            return this.data.markLabels.value.join(', ');
        } else {
            return '';
        }
    }
}
