import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { ImageElem, AutoCompleteMap, ModelInfo, AnnotationMeta } from './common/edit-data';
import { NetworkService } from './network.service';

@Injectable({
    providedIn: 'root'
})
export class EditService {

    public imageSubject = new BehaviorSubject<ImageElem>(null);
    public autoCompleteSubject = new BehaviorSubject<AutoCompleteMap>({});

    constructor(private network: NetworkService) {

    }

    parseImage(image: Blob, path: string, meta: AnnotationMeta = null, init = false) {
        const reader = new FileReader();
        reader.onload = (_: Event) => {
            const imgElem = new ImageElem(path, image, reader.result as string, meta);
            this.imageSubject.next(imgElem);
        };
        reader.readAsDataURL(image);
    }

    removeImage() {
        this.imageSubject.next(null);
    }

    ignoreImage() {
        return this.network.ignoreImage(this.image);
    }

    updateAutoComplete(modelInfo: ModelInfo, checked: boolean) {
        const map = this.autoCompleteSubject.getValue();
        map[modelInfo.key] = checked;
        this.autoCompleteSubject.next(map);
    }

    get image() {
        return this.imageSubject.getValue();
    }
}
