import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { TAB, COMMA, ENTER } from '@angular/cdk/keycodes';
import { MatChipInputEvent } from '@angular/material';
import { FormArray, FormControl, FormGroup } from '@angular/forms';
import { Feedback } from 'src/app/common/verification-data';
import { FeedbackService } from 'src/app/edit/common/feedback.service';

@Component({
    selector: 'app-label-chip-list',
    templateUrl: './label-chip-list.component.html',
    styleUrls: [
        './label-chip-list.component.scss',
        '../overall.component.scss'
    ]
})
export class LabelChipListComponent implements OnInit {

    @Input() editable: boolean;
    @Input() formGroup: FormGroup;
    @Input() formKey: string;
    @Input() focusedFeedback: Feedback;
    @Input() focusedFeedbackKey: string;

    public readonly separatorKeyCodes = [ENTER, COMMA, TAB];
    public labelForms: FormArray;

    constructor(public fs: FeedbackService) { }

    ngOnInit() {
        this.labelForms = this.formGroup.get(this.formKey) as FormArray;
    }

    add(event: MatChipInputEvent) {
        const input = event.input;
        const value = event.value;

        if ((value || '').trim()) {
            this.labelForms.push(new FormControl(value));
        }

        if (input) {
            input.value = '';
        }
    }

    remove(label: string) {
        const index = this.labels.indexOf(label);
        this.labelForms.removeAt(index);
    }

    get labels() {
        return this.labelForms.value;
    }

}
