import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { LegendAnnotation, KV } from 'src/app/common/verification-data';

@Component({
    selector: 'app-legend-spec-card',
    templateUrl: './legend-spec-card.component.html',
    styleUrls: [
        './legend-spec-card.component.scss',
        '../v-overall.component.scss'
    ]
})
export class LegendSpecCardComponent implements OnInit {

    @Input() data: LegendAnnotation;
    @Input() focusedKey: string;
    @Output() focus = new EventEmitter<string>();

    constructor() {

    }

    ngOnInit() { }

    emit(el: KV<any>) {
        this.focus.emit(el.key);
    }

    get labels(): string {
        if (this.data && this.data.labels.value.length > 0) {
            return this.data.labels.value.join(', ');
        } else {
            return '';
        }
    }
}
