import { Config } from './config';
import { Feedback } from './verification-data';

export interface MResponse<T> {
    message: string;
    code: number;
    data: T;
}

export interface Progress {
    total: number;
    approved: number;
    ignored: number;
}

export class ModelInfo {
    public key: string;
    public score: number | {[key: string]: number};
    public name: string;

    constructor(raw: any) {
        this.key = raw['key'];
        this.name = raw['name'];
        this.score = raw['score'];
    }

    get scoreFormat(): string {
        if (typeof this.score === 'number') {
            return (this.score as number).toFixed(2);
        } else {
            return Object.values(this.score)
                .map(d => d.toFixed(2))
                .join(' / ');
        }
    }
}

export interface AutoCompleteMap {
    [key: string]: boolean;
}

export class ImageElem {

    public path: string;
    public meta: AnnotationMeta;
    public blob: Blob;
    public index: number;
    public src: string;
    public semi: boolean;
    public name: string;

    constructor(path: string, blob?: Blob, src?: string,
                meta?: AnnotationMeta) {
        this.path = path;
        this.blob = blob;
        this.src = src;
        this.meta = meta;
        this.semi = false;
        const splited = this.path.split('/');
        this.name = splited[splited.length - 1];
    }

    get hasPrevAnnotation(): boolean {
        return !!this.meta && !!this.meta.annotation;
    }
}

export class PredictData {
    public semi: boolean;
    public labels: { [key: string]: any };

    constructor(raw: any) {
        this.semi = raw['semi'];
        this.labels = raw['labels'];
    }

    getBoundingBox(key: string, width: number, height: number): number[] | void {
        if (this.labels[key][0] < 0.5) {
            return null;
        } else {
            const label = this.labels[key].slice(1);
            label[0] *= width;
            label[2] *= width;
            label[1] *= height;
            label[1] *= height;
            return label;
        }
    }
}

export class AnnotatedImageData {
    public path: string;
    public annotation: {[key: string]: any };

    constructor(raw: any) {
        this.path = raw['path'];
        this.annotation = raw['annotation'];
    }
}

export interface Attr {
    value: number | string;
    viewValue: string;
}

export interface InstanceStatus {
    config?: Config;
    active: boolean;
}

export class AnnotationMeta {
    path: string;
    feedbacks: Feedback[];
    annotation?: { [key: string]: any };

    constructor(raw: any) {
        this.path = raw['path'];
        this.feedbacks = raw['feedbacks'];
        this.annotation = raw['annotation'] || null;
    }
}
