import { Component, OnInit, Input, ViewChild, SimpleChanges, OnChanges, Output, EventEmitter, ElementRef } from '@angular/core';
import { MatStepper, MatRadioChange, MatRadioGroup } from '@angular/material';
import { Classification } from 'src/app/common/task';
import { FormGroup } from '@angular/forms';
import { EditService } from 'src/app/edit.service';
import { Observable } from 'rxjs';
import { FeedbackService } from '../../common/feedback.service';

@Component({
    selector: 'app-classification',
    templateUrl: './classification.component.html',
    styleUrls: [
        './classification.component.scss',
        '../../edit-module.scss',
    ]
})
export class ClassificationComponent implements OnInit, OnChanges {

    @Input() formGroup: FormGroup;
    @Input() stepper: MatStepper;
    @Input() index: number;
    @Input() task: Classification;
    @Input() currIndex: number;
    @Input() event: Observable<string>;
    @Input() spacePressed: boolean;
    @Output() submitEmitter = new EventEmitter<void>();

    @ViewChild(MatRadioGroup, { static: true }) group: MatRadioGroup;
    @ViewChild('radioGroup', { static: true }) radioGroupRef: ElementRef;

    constructor(private edit: EditService, private fs: FeedbackService) { }

    ngOnInit() {
        this.event.subscribe(key => {
            if (key === 'predict' && this.edit.autoCompleteSubject.getValue()[this.task.key]) {
                this.task.applyPrediction(this.formGroup);
            }
        });

        this.fs.feedbackObservable.subscribe(feedback => {
            if (feedback.taskKey === this.task.key) {
                const el = this.radioGroupRef.nativeElement as HTMLElement;
                const { left, top } = el.getBoundingClientRect() as ClientRect;
                this.fs.showFeedback(left, top, feedback);
            }
        })
    }

    ngOnChanges(changes: SimpleChanges) {
        if (changes.currIndex
            && !changes.currIndex.isFirstChange()
            && this.index > 0 && changes.currIndex.currentValue === this.index) {
            const annotationLength = Object.keys(this.formGroup.value).length;
            if (annotationLength - 1 === this.index) {
                this.submitEmitter.emit();
            }
        }
    }

    onChange(change: MatRadioChange) {
        this.formGroup.get(this.task.key).setValue(change.value);
        if (this.index < this.stepper.steps.length - 1) {
            this.stepper.next();
        }
    }

}
